import React, { useState } from "react";

function ConsultHistory(props) {
  const [UpdateStates, setUpdateStates] = useState([]);
  const [dummyDatas, setDummyDatas] = useState(props.dummyDatas);

  const consultUpdateHandler = (index, type) => {
    let currentIdx = UpdateStates.indexOf(index);
    let copy = [...UpdateStates];
    if (currentIdx === -1) copy.push(index);
    else copy.splice(currentIdx, 1);
    setUpdateStates(copy);
  }
  return (
    <table className="table consult-history-table">
      {dummyDatas && dummyDatas.map((el, idx) => (
        <tr>
          <td className="ta-l p-10">
            {
               el.employeeRole == 1 ? <i className="employee-code employee-receipt">접수</i>
               : el.employeeRole == 2 ? <i className="employee-code employee-sangdamsa">상담</i>
               : el.employeeRole == 3 ? <i className="employee-code employee-lawyer">변호사</i>
               : el.employeeRole == 4 ? <i className="employee-code employee-naesa">내사</i>
               : null
            }
            <span className="employee-name">{el.employeeName}</span>
            <p>{el.consultDate}</p>
          </td>
          <td className="ta-l w-70p">
              {
                UpdateStates.indexOf(idx) === -1 
                ? <p className="consult-text">{el.consultCon}</p>
                :    
                <section className="update-area">
                  <textarea className="common-input consult-update-input w-100p" value={el.consultCon}></textarea>
                </section>
              }
          </td>
          <td className='w-12p'>
              { UpdateStates.indexOf(idx) === -1 
                ? <>
                    <span className="default-btn-green mr-5" onClick={()=>{consultUpdateHandler(idx, 'modify')}}>수정</span>
                    <span className="default-btn-red">삭제</span>
                  </>
                : <>
                    <span className="default-btn-green mr-5" onClick={()=>{consultUpdateHandler(idx, 'modify-complete')}}>수정완료</span>
                    <span className="default-btn-red" onClick={()=>{consultUpdateHandler(idx, 'modify-cancel')}}>수정취소</span>
                  </>
              }
          </td>
        </tr>
      ))}
    </table>
  );
}

export default ConsultHistory;
