import React, { useState, useRef, useEffect, useCallback } from 'react'
import './Sections/SchedulerPage.css';
import axios from 'axios';

// fontawesome Icon
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAngleLeft, faAngleRight } from "@fortawesome/free-solid-svg-icons";

function SchedulerPage() {
    const [scheduleArray, setScheduleArray] = useState([]);
    const yearText = useRef(); 
    const monthText = useRef();
    let current_year = (new Date()).getFullYear();
    let current_month = (new Date()).getMonth() + 1 >= 10 ? (new Date()).getMonth() + 1 : "0" + ((new Date()).getMonth() + 1);
    let current_date = (new Date()).getDate() >= 10 ? (new Date()).getDate() : "0" + (new Date()).getDate();
    let todayFullString = String(current_year) + String(current_month) + String(current_date);

    useEffect(() => {
        changeYearMonth(current_year, current_month);
    }, []);

    const checkLeapYear = (year) => { // 윤년구하기 함수
        if (year % 400 == 0) {
          return true;
        } else if (year % 100 == 0) {
          return false;
        } else if (year % 4 == 0) {
          return true;
        } else {
          return false;
        }
      };

      const getFirstDayOfWeek = (year, month) => { // 해당하는 달의 시작 "요일" 구하는 함수
        if (month < 10) month = "0" + month;
        return (new Date(year+"-"+month+"-01")).getDay();
      };

      const getYoil = (year, month, date) => {
          return ['일','월','화','수','목','금','토'][(new Date(year+"-"+month+"-"+date)).getDay()];
      }

      const changeYearMonth = (year, month) => { // 날짜의 값이 변화될때 발생하는 함수
        let month_day = [31,28,31,30,31,30,31,31,30,31,30,31];
        if (month == 2) {
          if (checkLeapYear(year)) month_day[1] = 29;
        }
        let first_day_of_week = getFirstDayOfWeek(year, month);
        let arr_calendar = [];
        for (let i = 0; i < first_day_of_week; i++) {
          arr_calendar.push({
            fullDate: '',
            isThisMonth: false,
          });
        }
        for (let i = 1; i <= month_day[month - 1]; i++) {
          let dates = i >= 10 ? i : "0" + i; 
          arr_calendar.push({
              fullDate: String(year) + String(month) + String(dates),
              isThisMonth: true,
              yoil: getYoil(year, String(month), String(dates)),
          });
        }
      
        let remain_day = 7 - (arr_calendar.length % 7);
        if (remain_day < 7) {
          for (let i = 0; i < remain_day; i++) {  
            arr_calendar.push({
                fullDate: '',
                isThisMonth: false,
            });
          }
        }
        
        getHolidaysFn(year, month, arr_calendar);
      };

      const getHolidaysFn = (year, month, arr_calendar) => { // 해당달의 공휴일 구하는 함수
        var url = '/getRestDeInfo'; /*URL*/
        var queryParams = '?' + encodeURIComponent('serviceKey') + '='+'NTWdkG62EVKF52INtffiGqRLpapnPGMHjBdZEN1DKyci0NYP5xmYgxsEtf6o%2Fk18DgQsyNhS1S6hY7tXo3HWqA%3D%3D'; /*Service Key*/
        queryParams += '&' + encodeURIComponent('solYear') + '=' + encodeURIComponent(String(year)); /* 년도 */
        queryParams += '&' + encodeURIComponent('solMonth') + '=' + encodeURIComponent(String(month)); /* 월 */

        axios.get(url + queryParams).then(response => {
            setScheduleArray(arr_calendar);
        })
        .catch((err) => {
            alert('스케쥴러를 불러오는 과정에서 오류가 발생하였습니다.');
            console.log('오류 내용 : ', err);
        })
      };

      const changeMonth = useCallback((diff) => { // 버튼 클릭해서 월 이동 함수
          current_month = current_month + diff;
          if (current_month == 0) {
            current_year = current_year - 1;
            current_month = 12;
          } else if (current_month == 13) {
            current_year = current_year + 1;
            current_month = 1;
          }
          yearText.current.textContent = current_year;
          monthText.current.textContent = current_month;
          changeYearMonth(current_year, current_month);
      }, [yearText.current, monthText.current]);

      const setToday = useCallback(() => { // 오늘 달력으로 이동하기 함수
        current_year = (new Date()).getFullYear();
        current_month = (new Date()).getMonth() + 1;
        yearText.current.textContent = current_year;
        monthText.current.textContent = current_month;
        changeYearMonth(current_year, current_month);
      }, [yearText.current, monthText.current]);

      const renderCalcTable = () => {
        let resultHTML = [];
        for (let i = 0; i < parseInt(scheduleArray.length / 7); i++) {
            resultHTML.push(
                <section className='scheduler-sect week-calc'>
                    <p className='date-num'>주간합계</p>
                    <table className="scheduler-table">
                        <tr>
                            <th></th>
                            <th>건수</th>
                            <th>금액</th>
                        </tr>
                        <tr>
                            <th>접수</th>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr>
                            <th>내사</th>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr>
                            <th>신규수임</th>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr>
                            <th>상담</th>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr>
                            <th>추가수임</th>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr>
                            <th>성공보수</th>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr>
                            <th>미수금</th>
                            <td></td>
                            <td></td>
                        </tr>
                    </table>
                </section>
            )
        }
        return resultHTML;
      };

    return (
        <div className='page-container scheduler-page'>   
            <div className="btn-wrapper clearfix">
                <span 
                onClick={setToday}
                className="go-today default-btn-theme">TODAY</span>

                <p className="month-btn">
                    <span 
                    onClick={()=>{changeMonth(-1)}}
                    className="default-btn"><FontAwesomeIcon icon={faAngleLeft}/></span>
                    <span 
                    onClick={()=>{changeMonth(1)}}
                    className="default-btn"><FontAwesomeIcon icon={faAngleRight}/></span>
                </p>
            </div>

            <div className="calendar-wrapper mt-20">
            
                <p className='date-txt mb-30'>
                    <span ref={yearText}>{current_year}</span>년 
                    <span className="ml-5" ref={monthText}>{current_month}</span>월
                </p>

                <div className="th-sect clearfix">
                    <div className="date-head">
                        <span className="yoil sunday">일</span>
                        <span className="yoil">월</span>
                        <span className="yoil">화</span>
                        <span className="yoil">수</span>
                        <span className="yoil">목</span>
                        <span className="yoil">금</span>
                        <span className="yoil">토</span>
                    </div>

                    <div className="calc-head">
                        <span className="calc">합계</span>
                    </div>
                </div>

                <div className="td-sect clearfix">
                    <div className="date-body">
                    {scheduleArray.map((el, idx) => {
                        return !el.isThisMonth
                        ? <section className='scheduler-sect week notMonth'>
                          </section>
                        : el.yoil === "일"
                        ?  <section className={
                            `scheduler-sect week sunday 
                            ${el.fullDate === todayFullString ? 'today' : ''}
                            ${el.isHoliday ? 'holiday' : ''}
                            `}>
                            <p className='date-num'>{el.fullDate.slice(-2)}</p>
                           </section>
                        : <section className={`scheduler-sect week 
                                               ${el.fullDate === todayFullString ? 'today' : ''} 
                                               ${el.yoil === '토' ? 'saturday' : 'weekdays'}
                                               ${el.isHoliday ? 'holiday' : ''}
                                               `}>
                            <p className='date-num'>{el.fullDate.slice(-2)}</p>
                            <table className="scheduler-table">
                                <tr>
                                    <th></th>
                                    <th>건수</th>
                                    <th>금액</th>
                                </tr>
                                <tr>
                                    <th>접수</th>
                                    <td></td>
                                    <td></td>
                                </tr>
                                <tr>
                                    <th>내사</th>
                                    <td></td>
                                    <td></td>
                                </tr>
                                <tr>
                                    <th>신규수임</th>
                                    <td></td>
                                    <td></td>
                                </tr>
                                <tr>
                                    <th>상담</th>
                                    <td></td>
                                    <td></td>
                                </tr>
                                <tr>
                                    <th>추가수임</th>
                                    <td></td>
                                    <td></td>
                                </tr>
                                <tr>
                                    <th>성공보수</th>
                                    <td></td>
                                    <td></td>
                                </tr>
                                <tr>
                                    <th>미수금</th>
                                    <td></td>
                                    <td></td>
                                </tr>
                            </table>
                         </section>
                        })}
                    </div>

                    <div className='calc-body'>
                        {renderCalcTable()}
                    </div>
                </div>
            </div>
        </div>
    )
}

export default SchedulerPage