import React, { useState } from 'react'
import './Sections/LoginPage.css';
import { company, loginInfo } from '../../../Datas/datas';
import Modal from '../../utils/Modal';
import { withRouter } from 'react-router-dom';

// fontawesome Icon
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faUser, faKey } from "@fortawesome/free-solid-svg-icons";

function LoginPage(props) {
    const [InputId, setInputId] = useState('');
    const [InputPwd, setInputPwd] = useState('');
    const [rememberId, setrememberId] = useState(false);
    const [modal, setModal] = useState({ status: false, text: '' });

    const closeModal = () => {
        setModal(false);
    };
    const submitHandler = (e) => {
        e.preventDefault();
        if ((InputId !== loginInfo.userId) || (InputPwd !== loginInfo.userPwd)) {
            let copy = {...modal};
            copy.status = true;
            copy.text = '아이디 또는 비밀번호를 잘못 입력하셨습니다.';
            setModal(copy);
            setInputPwd('');
        } else {
            props.history.push('/receipt');
        }
    };
    
    return (
        <div className="login-page">
            
            <form className="login-form" onSubmit={submitHandler}>
                <p className="title">{company} - LOGIN</p>

                <p className="row">
                    <FontAwesomeIcon icon={faUser} className="login-ico"/>
                    <input type="text" className="login-input" placeholder="아이디를 입력하세요" value={InputId} onChange={(e)=>{setInputId(e.target.value)}} required/>
                </p>
                <p className="row">
                    <FontAwesomeIcon icon={faKey} className="login-ico"/>
                    <input type="password" className="login-input" placeholder="비밀번호룰 입력하세요" value={InputPwd} onChange={(e)=>{setInputPwd(e.target.value)}} required/>
                </p>

                <p className="checkbox-wrap mt-20">
                    <input type="checkbox" onChange={()=>{setrememberId(!rememberId)}} checked={rememberId} id="remeber_me"/>
                    <label htmlFor="remeber_me">remeber_me</label>
                </p>
                <button className="login-btn default-btn-theme">로그인</button>
            </form>

            {modal.status && <Modal refreshFunction={closeModal} modalText={modal.text}/>}
        </div>
    )
}

export default withRouter(LoginPage);
