import React, { useState } from "react";
import SearchBox from "../../utils/SearchBox";
import styled from "styled-components";

const LongTh = styled.th`
  width: 117px;
`
function CaseListPage() {
  
  const renderTable = () => {
        const results = [];
        for (let i = 0; i < 20; i++) {
            results.push(
                <tr key={i}>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                </tr>
            );
        }
        return results;
  }

  return (
    <div className="page-container">
      <SearchBox option={{
        dateStart: true,
        dateEnd: true,
        isNew: true,
        jeopsuEmployee: true,
        searchText: true,
        caseName2: true
      }}/>

      <div className="table-title clearfix">
        <p className="title-txt">사건진행관리</p>

        <a href="javascript:;" className="default-btn-theme">전체보기</a>
      </div>

      <div className="table_wrapper">
        <table className="table">
          <thead>
            <tr>
                <th>구분</th>
                <LongTh>사건기록관리번호</LongTh>
                <th>의뢰인</th>
                <th>사건명(중)</th>
                <th>사건번호</th>
                <th>수임일</th>
                <th>일자</th>
                <th>처리현황</th>
                <th>담당자</th>
                <th>상대방</th>
                <th>계속기관</th>
                <th>기일명</th>
                <th>기일(날짜)</th>
                <th>특이사항(송무)</th>
                <th>진행잔여일</th>
            </tr>
          </thead>
          <tbody>
              {renderTable()}
          </tbody>
        </table>
      </div>

    </div>
  );
}

export default CaseListPage;
