import React, { useState } from "react";
import SearchBox from "../../utils/SearchBox";
import articleLists from '../../../Datas/articleLists3';
import { visitStatus, visitCancelReason } from '../../../Datas/datas';

function VisitListPageByPeriod() {
  const [Article, setArticle] = useState(articleLists);
  const [ConsultRoom, setConsultRoom] = useState(articleLists.map(el => el.consultRoom));
  const [CarInfo, setCarInfo] = useState(articleLists.map(el => el.car));

  const consultRoomHandler = (e, index) => {
    let copy = [...ConsultRoom];
    copy[index] = e.target.value;
    setConsultRoom(copy);
  };
  const carHandler = (e, index) => {
    let copy = [...CarInfo];
    copy[index] = e.target.value;
    setCarInfo(copy);
  };

  return (
    <div className="page-container ">
      <SearchBox option={{
        dateStart: true,
        dateEnd: true,
        searchText: true
      }}/>

      <div className="table-title clearfix">
        <p className="title-txt">기간별내사현황</p>

        <a href="javascript:;" className="default-btn">엑셀다운</a>
      </div>

      <div className="table_wrapper">
        <table className="table">
          <thead>
            <tr>
              <th className="w-100">일자</th>
              <th>구분</th>
              <th className="w-80">내사시간</th>
              <th>의뢰인</th>
              <th>사건명(중)</th>
              <th>팀(부서)</th>
              <th>상담매니저</th>
              <th>상담변호사1</th>
              <th>상담변호사2</th>
              <th>상태</th>
              <th>상담실</th>
              <th>차량</th>
              <th>내사취소사유</th>
              <th>내사상담결과</th>
            </tr>
          </thead>
          <tbody>
            {
                Article.map((el,idx)=>(
                    <tr key={idx}>
                      <td>{el.date}</td>
                      <td>{el.sort}</td>
                      <td>{el.time}</td>
                      <td>{el.customer}</td>
                      <td>{el.caseName2}</td>
                      <td>{el.team}</td>
                      <td>{el.consultManager}</td>
                      <td>{el.consultLawyer1}</td>
                      <td>{el.consultLawyer2}</td>
                      <td>
                        <select className="common-input w-100p">
                          {
                            visitStatus.map((element,index)=>(
                              <option value={element} selected={element === el.status}>{element}</option>
                            ))
                          }
                        </select>
                      </td>
                      <td>
                        <input type="text" className="common-input w-100p" value={ConsultRoom[idx]} onChange={(e)=>{consultRoomHandler(e, idx)}}/>
                      </td>
                      <td>
                        <input type="text" className="common-input w-100p" value={CarInfo[idx]} onChange={(e)=>{carHandler(e, idx)}}/>
                      </td>
                      <td>
                        <select className="common-input w-100p">
                          {
                            visitCancelReason.map((element,index)=>(
                              <option value={element} selected={element === el.cancelReason}>{element}</option>
                            ))
                          }
                        </select>
                      </td>
                      <td>{el.consultResult}</td>
                    </tr>
                ))
            }
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default VisitListPageByPeriod;
