import React from 'react'
import SearchBox from '../../utils/SearchBox';
import styled from 'styled-components';

const DbTitleLine = styled.td`
    background: #565656;
    color:#fff;
    font-size:15px;
`;

function AllStatisticsPage() {
    return (
        <div className='page-container'>
            <SearchBox option={{
                dateStart: true,
                dateEnd: true,
                searchText: true
            }}/>
             
            <div className="table-title clearfix">
                <p className="title-txt">통계[종합]</p>
                <a href="javascript:;" className="default-btn">엑셀다운</a>
            </div>

            <table className="table">
                <thead>
                    <tr>
                        <th>디비수</th>
                        <th>내사예약수</th>
                        <th>예약률</th>
                        <th>내사수</th>
                        <th>내사율</th>
                        <th>수임수</th>
                        <th>수임률</th>
                        <th>객단가</th>
                        <th>매출</th>
                        <th>상담수</th>
                        <th>상담료</th>
                        <th>총매출</th>
                    </tr>
                </thead>
                <tbody>
                    <tr><DbTitleLine colSpan={12}>인콜 DB</DbTitleLine></tr>
                    <tr>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                    </tr>
                    <tr><DbTitleLine colSpan={12}>접수 DB</DbTitleLine></tr>
                    <tr>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                    </tr>
                    <tr><DbTitleLine colSpan={12}>종합</DbTitleLine></tr>
                    <tr>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                    </tr>
                </tbody>
            </table>

            
        </div>
    )
}

export default AllStatisticsPage
