import React from 'react'
import SearchBox from '../../utils/SearchBox';
import Paging from '../../utils/Paging';
import { Link } from 'react-router-dom';
import styled from 'styled-components';

const SendFail = styled.span`
    color: #f30
`

function SmsHistorypage() {
    return (
        <div className="page-container">
            <SearchBox option={{
                dateStart: true,
                dateEnd: true,
                searchText: true
             }}/>
             
             <div className="table-title clearfix"><p className="title-txt">문자전송 내역</p></div>

             <div className="table_wrapper">
                <table className="table">
                    <thead>
                        <tr>
                            <th>No.</th>
                            <th>이름</th>
                            <th>전화번호</th>
                            <th>상담사</th>
                            <th>접수일</th>
                            <th>결과</th>
                            <th>전송시간</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>2</td>
                            <td><Link to='/mainUser'>홍길동</Link></td>
                            <td>010-1234-1234</td>
                            <td>홍길동</td>
                            <td>2021-09-11</td>
                            <td><SendFail>실패</SendFail></td>
                            <td>2021-09-18 00:00:00</td>
                        </tr>
                        <tr>
                            <td>2</td>
                            <td><Link to='/mainUser'>홍길동</Link></td>
                            <td>010-1234-1234</td>
                            <td>홍길동</td>
                            <td>2021-09-11</td>
                            <td>성공</td>
                            <td>2021-09-18 00:00:00</td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <Paging/>

        </div>
    )
}

export default SmsHistorypage
