import React from "react";
import SearchBox from "../../utils/SearchBox";
import { currentDate } from '../../utils/time';
import './Sections/DetailSalesListPage.css';

function DetailSalesListPage() {
    const renderTable = () => {
        const results = [];
        for (let i = 0; i < 20; i++) {
            results.push(
                <tr key={i}>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                </tr>
            );
        }
        return results;
    }

    return (
        <div className="page-container detail-sales-list">
            <SearchBox option={{
                dateStart: true,
                dateEnd: true,
                incomeKinds: true,
                jeopsuEmployee: true,
                searchText: true
            }}/>

            <div className="table-title clearfix">
                <p className="title-txt">매출상세관리</p>

                <a href="javascript:;" className="default-btn">엑셀다운</a>
            </div>  

            <div className="table_wrapper">
                <table className="table">
                <thead>
                    <tr>
                        <th>결제일자</th>
                        <th>수임료구분</th>
                        <th>의뢰인</th>
                        <th>사건명(중)</th>
                        <th>수납구분</th>
                        <th>약정금액</th>
                        <th>결제금액</th>
                        <th>공급가액</th>
                        <th>부가세</th>
                        <th>수수료율</th>
                        <th>수수료</th>
                        <th>순매출</th>
                        <th>결제방법</th>
                        <th>상담매니저</th>
                        <th>상담변호사</th>
                        <th className="w-200">비고</th>
                    </tr>
                    <tr>
                        <th>합계</th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th className="w-200"></th>
                    </tr>
                    <tr>
                        <th>일간 ({currentDate().slice(-2)}일)</th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th className="w-200"></th>
                    </tr>
                </thead>
                <tbody>
                    {renderTable()}
                </tbody>
                </table>
            </div>
        </div>
        
    )
}

export default DetailSalesListPage
