import React, { useState, useEffect } from 'react';
import { currentDate, currentYear, currentMonth } from '../../utils/time.js';
import styles from './Sections/ReportAgreementPage.module.css';
import { useLocation } from "react-router-dom";

function ReportAgreementPage(props) {
  const { pathname } = useLocation();
  const [caseCode, setCaseCode] = useState(props.match.params.caseCode);

    useEffect(() => {
        if (!caseCode) {
            alert('올바르지 않은 접근입니다.');
            window.close();
            return false;
        }

        if (pathname.split('/')[1] === 'reportAgreementPrint') {
            window.print();
        } 

    }, [caseCode])

    return (
        <>
        { caseCode && 
            (   
                <div>
                    <div className='book'>
                        <div className='page-component' id='pages1'>
                            <div className={styles.subpage}>
                                <h2 className={styles.print_tit}>사건위임약정서</h2>
                                <section className={`${styles.part} mb-10`}>
                                    <article className={styles.part_tit}>갑(위임인)</article>
                                    <article className={styles.part_con}>
                                        {/* 본인 정보 */}
                                        <p>
                                            <span className='gap_name'>홍길동</span>  
                                            (<span className='gap_jumin'>000000-000000</span> ; <span className='gap_phone'>010-0000-0000</span>)
                                        </p>
                                        <p>
                                            <span className='gap_address'>서울시 동대문구 장안동 000-000, 101동 101호</span>
                                        </p>

                                        {/* 대리인 정보 */}
                                        <p>
                                            대리인 : <span className="deputy_name">김철수</span>
                                            (<span className='deputy_jumin'>000000-000000</span> ; <span className='deputy_phone'>010-0000-0000</span>)<br/>
                                            (관계 : <span className='deputy_relation'>사실혼 배우자</span>)
                                        </p>
                                        <p>
                                            <span className='deputy_address'>서울시 동대문구 장안동 000-000, 101동 101호</span>
                                        </p>
                                    </article>
                                </section>
                                <section className={styles.part}>
                                    <article className={styles.part_tit}>을(수임인)</article>
                                    <article className={styles.part_con}>
                                        <p>
                                            <span className='eul_name'>법무법인(유한) 이현</span>  
                                            (<span className='eul_company_num'>000000-0000000</span>)  
                                        </p>
                                        <p><span className='eul_company_address'>서울 서초구 사임당로 32, 1, 7층(서초동, 재우빌딩)</span></p>
                                        <p><span className='eul_representative_name'>대표변호사 이환권</span></p>
                                    </article>
                                </section>

                                <p className={styles.noti_con}>갑과 을은 다음과 같이 사건위임약정을 체결합니다.</p>

                                <h3 className={styles.print_tit_small}>다 음</h3>

                                <div className={styles.report_article}>
                                    <p className={styles.article_title}>1. (위임사무)</p>
                                    <p className='mb-20'>갑이 상대방 김영희에 대하여 할 아래의 업무</p>
                                    <ul className={styles.korean_list}>
                                        <li><span>위임사무 업무1</span></li>
                                        <li><span>위임사무 업무2</span></li>
                                        <li><span>위임사무 업무3</span></li>
                                        <li><span>위임사무 업무4</span></li>
                                        <li><span>위임사무 업무5</span></li> 
                                    </ul>
                                </div>

                                <div className={styles.report_article}>
                                    <p className={styles.article_title}>2. (위임보수)</p>

                                    <ul className={styles.korean_list}>
                                        <li><span>갑은 을에게 위 제1조 위임사무에 대한 착수보수로 0원(부가가치세 포함)을 지급한다.</span></li>
                                        <li><span>이 약정서가 체결된 이후에는, 을의 책임 없는 사유 및 당사자 간의 합의(갑이 을을 통하지 않고 상대방과 합의한 경우 포함)로 인하여 위임사무가 종료되는 경우 갑은 을에게 지급한 착수보수금의 반환을 청구할 수 없다.</span></li>
                                        <li><span>성공보수 : 성공보수 지급요건(부가세포함)</span></li>
                                        <li><span>위 제 1조 n항을 추가로 진행하게 될 경우 착수보수로 100,000원(부가가치세포함)을 지급하기로 한다.</span></li>
                                        <li><span>특약사항 : 특약사항 텍스트더미데이터</span></li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className='book'>
                        <div className='page-component' id='pages2'>
                            <div className={styles.subpage}>

                                <div className={styles.report_article}>
                                    <p className={styles.article_title}>3. (위임사무처리비용)</p>
                                    <ul className={styles.korean_list}>
                                        <li>
                                            <span>을이 위임사무를 처리하는 과정에서 발생하는 재판비용(등록면허세, 신문공고비 등)은 갑이 부담한다.</span>
                                        </li>
                                        <li>
                                            <span>을은 위임사무를 처리하는 과정에서 위 가. 항 비용을 갑에게 청구한다</span>
                                        </li>
                                    </ul>
                                </div>

                                <div className={styles.report_article}>
                                    <p className={styles.article_title}>4. (계약해지)</p>
                                    <p>갑은, 을이 고의나 중과실로 이 위임약정에 위반하는 행위를 하거나 의무를 이행하지 않는 경우 외에는, 이 위임약정을 해지하지 않는다.</p>
                                </div>

                                <div className={styles.report_article}>
                                    <p className={styles.article_title}>5. (통지의무)</p>
                                    <p>을은 위임사무의 중요한 처리상황 및 그 결과를 갑에게 통지하고, 위임이 종료한 때에는 그 결과를 갑에게 지체 없이 통지하여야 한다.</p>
                                </div>

                                <div className={styles.report_article}>
                                    <p className={styles.article_title}>6. (인장조각)</p>
                                    <p>갑은, 을이 법원에 제출하는 소송위임장 등 각종 위임장에 갑 명의의 인장을 만들어서 위임인의 날인을 대행하는 것에 동의한다.</p>
                                </div>

                                <div className={styles.report_article}>
                                    <p className={styles.article_title}>7.	이 약정은 서명날인(무인) 또는, 문자를 통해 ‘동의’하는 즉시 효력이 발생된다.</p>
                                </div>
                                <div className={styles.report_article}>
                                    <p className={styles.article_title}>8.	갑이 을에게 제출한 증거자료는 사건 종결 시 즉시 반환하기로 한다. 만약 갑이 증거자료의 파기를 희망하는 경우 을이 그 즉시 파기하기로 한다.</p>
                                </div>
                                <div className={styles.report_article}>
                                    <p className={styles.article_title}>9.	갑은 을로부터 이 약정서의 내용을 상세히 잘 들었으며, 이 약정서 부본을 교부받았음을 확인한다.</p>
                                </div>

                                <section className={styles.sign_wrap}>
                                    <p className={styles.sign_line}>
                                        <span className={styles.sign_tit}>서명</span>
                                        <span className={styles.sign_cont}>(인)</span>
                                    </p>
                                    <p className={styles.today}>{currentYear()}년 {currentMonth()}월 {currentDate().slice(-2)}일</p>
                                    <p className={styles.sign_line}>
                                        <span className={styles.sign_tit}>갑(위임인)</span>
                                        <span className={styles.sign_cont}>(인)</span>
                                    </p>
                                    <p className={styles.sign_line}>
                                        <span className={styles.sign_tit}>을(수임인) 법무법인(유한) 이현 대표 변호사</span>
                                        <span className={styles.sign_cont}>(인)</span>
                                    </p>
                                    <p className={styles.sign_line}>
                                        <span className={styles.sign_tit}>상담책임 변호사</span>
                                        <span className={styles.sign_cont}>(인)</span>
                                    </p>
                                    <p className={styles.sign_line}>
                                        <span className={styles.sign_tit}>상담변호사</span>
                                        <span className={styles.sign_cont}>(인)</span>
                                    </p>
                                </section>
                            </div>  
                        </div>
                    </div>
                </div>
            )
        }
        </>
    )
};

export default ReportAgreementPage;