import React, { useState, useEffect } from 'react';
import DatepickerCal from '../../../utils/DatepickerCal';
import { caseName1, caseName2, caseRole } from '../../../../Datas/datas';
import {
    parseOnlyPhone
} from '../../../utils/unitParse';

function BaedangManagepart(props) {
    
    return (
        <div className="common-part mb-20 mt-20">
            <section className="part-title clearfix">
                <p className="title-txt">배당관리</p>
                <p className="btn-wrapper">
                    <span 
                        className={ `default-btn-theme ml-5 ${props.PartShowAndHide.baedangManage ? 'minus' : 'plus'}` }
                        onClick={()=>{props.partToggle('baedangManage')}}>
                    </span>
                </p>
            </section>
            
            {
                props.PartShowAndHide.baedangManage
                &&
                    <section className="part-content">

                        {/* baedang-info-table */}
                        <table className='table baedang-info-table'>
                            <tr>
                                <th>배당정보</th>
                                <th>배당의견</th>
                                <td colSpan="3">
                                    <textarea className='common-input w-100p'></textarea>
                                </td>
                                <th>배당센터</th>
                                <td>
                                    <select className="common-input w-100p">
                                        <option value=""></option>
                                        <option value="">배당센터 예시1</option>
                                        <option value="">배당센터 예시2</option>
                                    </select> 
                                </td>
                                <th>배당결과</th>
                                <td>
                                    <select className="common-input w-100p">
                                        <option value=""></option>
                                        <option value="미배당">미배당</option>
                                        <option value="배당완료">배당완료</option>
                                    </select> 
                                </td>
                            </tr>
                            <tr>
                                <td colSpan='9'>
                                    <button className="default-btn-blue big-btn mt-5 mb-5">배당정보 저장</button>
                                </td>
                            </tr>
                        </table>
                        
                        
                        {/* // baedang-info-table */}

                    </section>
            }
        </div>
    )
}

export default BaedangManagepart
