import React, { useCallback } from 'react';
import styled from 'styled-components';

const CheckerWrapper = styled.p`
    border: 1px solid #ddd;
`;
const Checker = styled.span`
    float: left;
    height: 39px;
    cursor: pointer;
    display: inline-block;
    line-height: 39px;
    width: 33.3333%;
    border-right: 1px solid #ddd;
    background: ${ (props) => props.active ? '#bbb' : '#eee' };
    font-size: 14px;
    &: last-child {
        width:33.3334%;
        border-right: none;
    }
`;

function AuthChecker(props) {

    const onChangeCheck = useCallback((values) => {
        const copy = [...props.authSettings];
        const currentIdx = copy.findIndex(v => v.authId === props.authId);
        if (currentIdx !== -1) {
            copy[currentIdx][props.authNames] = values;
        }
        props.setAuthSettings(copy);
        },[]);

    return (
        <CheckerWrapper className="clearfix">
            <Checker onClick={()=>{onChangeCheck("입력")}} active={props.authValues === "입력"}>O</Checker>
            <Checker onClick={()=>{onChangeCheck("보기")}} active={props.authValues === "보기"}>V</Checker>
            <Checker onClick={()=>{onChangeCheck("권한없음")}} active={props.authValues === "권한없음"}>X</Checker>
        </CheckerWrapper>
    )
}

export default AuthChecker
