import React from 'react';
import SearchBox from '../../utils/SearchBox';
import InflowTable from './Sections/InflowTable';
import InflowByConsultant from './Sections/InflowByConsultant';

const dummyDatas = [
    {name: '미지정'},
    {name: '강벼리'},
    {name: '과거디비'},
    {name: '방치용'},
    {name: '안지숙'},
    {name: '이선영'},
    {name: '장호찬'},
    {name: '정종화'},
    {name: '황선미'},
]

function InflowChartPage() {
    return (
        <div className="page-container">
            <SearchBox option={{
                dateStart: true,
                dateEnd: true,
                searchText: true
            }}/>

            <div className="table-title clearfix">
                <p className="title-txt">유입 통계</p>
                <a href="javascript:;" className="default-btn">엑셀다운</a>
            </div>

            <InflowTable />
            <div className="table-title clearfix"><p className="title-txt">상담사 통계</p></div>
            
            {dummyDatas.map((el, idx) => (
                <InflowByConsultant dataValues={el.name}/>
            ))}
        </div>
    )
}

export default InflowChartPage
