import React, { useState, useEffect } from "react";
import "./Sections/MainUserPage.css";
import JeopSuPart from "./Sections/JeopSuPart.js";
import SangdamPart from "./Sections/SangdamPart.js";
import VisitSchedulePart from "./Sections/VisitSchedulePart.js";
import PaymanagePart from "./Sections/PaymanagePart.js";
import ContractManagePart from "./Sections/ContractManagePart.js";
import BaedangManagepart from "./Sections/BaedangManagepart.js";
import CaseManagePart from "./Sections/CaseManagePart.js";

function MainUserPage() {
  const [PartShowAndHide, setPartShowAndHide] = useState({
    jeopsu: true, // 접수관리
    sangdam: true, // 상담관리
    visitSchedule: true, // 내사일정
    paymanage: true, // 결제관리
    contractManage: true, // 계약관리
    // baedangManage: true, // 배당관리
    caseManage: true, // 사건진행관리
  });

  const partToggle = (target) => {
    let copy = { ...PartShowAndHide };
    copy[target] = !PartShowAndHide[target];
    setPartShowAndHide(copy);
  };

  const [userName, setUserName] = useState("홍길동");
  const [userPhone, setUserPhone] = useState("010-1234-1234");

  return (
    <div className="page-container main-user-page">
      <JeopSuPart
        PartShowAndHide={PartShowAndHide}
        partToggle={partToggle}
        setPartShowAndHide={setPartShowAndHide}
        userName={userName}
        setUserName={setUserName}
        userPhone={userPhone}
        setUserPhone={setUserPhone}
      />
      <SangdamPart 
        PartShowAndHide={PartShowAndHide} 
        partToggle={partToggle} 
        userName={userName}
        setUserName={setUserName}
        userPhone={userPhone}
        setUserPhone={setUserPhone}
      />
      <VisitSchedulePart
        PartShowAndHide={PartShowAndHide}
        partToggle={partToggle}
        userName={userName}
        setUserName={setUserName}
        userPhone={userPhone}
        setUserPhone={setUserPhone}
      />
      <PaymanagePart
        PartShowAndHide={PartShowAndHide}
        partToggle={partToggle}
        userName={userName}
        setUserName={setUserName}
        userPhone={userPhone}
        setUserPhone={setUserPhone}
      />
      <ContractManagePart
        PartShowAndHide={PartShowAndHide}
        partToggle={partToggle}
        userName={userName}
        setUserName={setUserName}
        userPhone={userPhone}
        setUserPhone={setUserPhone}
      />
      {/* <BaedangManagepart PartShowAndHide={PartShowAndHide} partToggle={partToggle}/> */}
      <CaseManagePart
        PartShowAndHide={PartShowAndHide}
        partToggle={partToggle}
        userName={userName}
        setUserName={setUserName}
        userPhone={userPhone}
        setUserPhone={setUserPhone}
      />

    </div>
  );
}

export default MainUserPage;
