import React, { useMemo } from "react";
import SearchBox from "../../utils/SearchBox";

function RefundListPage() {
    
    const redColor = useMemo(() => ({color: '#f30'}), []);

    const renderTable = () => {
        const results = [];
        for (let i = 0; i < 20; i++) {
            results.push(
                <tr key={i}>
                    <td>2021-01-01</td>
                    <td>2021-01-01</td>
                    <td>이동명</td>
                    <td>가사본안</td>
                    <td>3,500,000</td>
                    <td>3,500,000</td>
                    <td style={redColor}>-1,750,000</td>
                    <td>안지숙</td>
                </tr>
            );
        }
        return results;
    }

    return (
        <div className="page-container">
            <SearchBox option={{
                dateStart: true,
                dateEnd: true,
                searchText: true
            }}/>

            <div className="table-title clearfix">
                <p className="title-txt">환불리스트</p>
            </div>  

            <div className="table_wrapper">
                <table className="table">
                    <thead>
                        <tr>
                            <th>환불일자</th>
                            <th>약정일자</th>
                            <th>의뢰인</th>
                            <th>사건명(중)</th>
                            <th>약정금액</th>
                            <th>실입금액</th>
                            <th>환불금액</th>
                            <th>상담매니저</th>
                        </tr>
                    </thead>
                    <tbody>
                        {renderTable()}
                    </tbody>
                </table>
            </div>
        </div>
        
    )
}

export default RefundListPage
