import React, { useState } from "react";
import Navigation from '../Navigation/Navigation';
import './Sections/Header.css';
import { company } from '../../../Datas/datas';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

// fontawesome Icon
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faUser } from "@fortawesome/free-solid-svg-icons";

const UserInfoLists = styled.li`
  line-height: 18px;
`
const UserInfoLinks = styled.a`
  padding-left:10px;
  font-size:12px;
`

function Header() {

  const [IsOpen, setIsOpen] = useState(false);

  return (
    <div className='header-wrap'>
        <div className="header-top theme-bg-color clearfix">
          <div className="page-container">
          <Link className='logo' to="/">{company} ADMIN</Link>
          <div className='user-info-wrap'onMouseEnter={()=>{setIsOpen(true)}} onMouseLeave={()=>{setIsOpen(false)}}>
            <span className="user-info">
              <FontAwesomeIcon icon={faUser} className="mr-5"/>
              아이보스관리자(g1) - {company}
            </span>
            {
              IsOpen && 
              <ul className="user-info-menu balloon">
                <UserInfoLists>
                  <UserInfoLinks href="/" >Profile</UserInfoLinks>
                </UserInfoLists>
                <UserInfoLists>
                  <UserInfoLinks href="/" >Logout</UserInfoLinks>
                </UserInfoLists>
              </ul>
            }
          </div>
          </div>
        </div>

        {/* Navigation */}
          <Navigation />
        {/* // Navigation */}

    </div>
  );
}

export default Header;
