import React, { useState } from 'react';
import DatepickerCal from '../../../utils/DatepickerCal';
import { caseName1, caseName2, employee, lawyer, caseRole, baedangTeam, CaseDayName } from '../../../../Datas/datas';
import { parseOnlyPhone, parseCurrency } from '../../../utils/unitParse';
import Select from 'react-select';
import Modal from '../../../utils/Modal';
import { currentDate } from '../../../utils/time';
import instituitionLists from '../../../../Datas/instituitionLists';

function CaseManagePart(props) {
    const [modal, setModal] = useState({ status: false, text: '' }); // 모달 state

    const [caseInfo, setCaseInfo] = useState({ // 사건진행관리 state
        caseNumber: '', // 사건번호
        caseBig: '', // 사건명(대)
        caseMiddle: '', // 사건명(중)
        caseCodeNumber: '', // 사건코드번호
        affairInfo: '', // 위임사무
        caseManageNumber: '', // 사건기록관리번호
        caseManageCode: '', // 사건관리코드
        deadLineDate: currentDate(), // 목표종결일
        instituition: '서울강남경찰서', // 계속기관
        instituition2: '', // 계속부서
        caseDetail: '', // 사건명
        customer: '', // 의뢰인
        customerPhone: '', // 연락처
        customerAddress: '', // 주소
        customerEmail: '', // 이메일
        customerRole: '', // 법적지위
        opponent: '', // 상대방
        opponentPhone: '', // 상대방연락처
        opponentAddress: '', // 상대방주소
        payAmount: '', // 약정금액
        successPay: '', // 성공보수
        allPay: '', // 총진행비용
    });

    const {
        caseNumber,
        caseBig,
        caseMiddle,
        caseCodeNumber,
        affairInfo,
        caseManageNumber,
        caseManageCode,
        deadLineDate,
        instituition,
        instituition2,
        caseDetail,
        customer,
        customerPhone,
        customerAddress,
        customerEmail,
        customerRole,
        opponent,
        opponentPhone,
        opponentAddress,
        payAmount,
        successPay,
        allPay
    } = caseInfo;

    const [baedangInfo, setBaedangInfo] = useState( // 배당내역
        {baedangDate: currentDate(), songmuEmployee: '', songmuLawyer: '', team: '', endDate: currentDate(), etcCon: ''}
    );
    const [reBaedangInfo, setReBaedangInfo] = useState([ // 재배당일자
        {reBaedangDate: currentDate(), songmuEmployee: '', songmuLawyer: '', reason: ''},
        {reBaedangDate: currentDate(), songmuEmployee: '', songmuLawyer: '', reason: ''},
    ]);
    const [caseProcess, setCaseProcess] = useState([// 사건진행정보
        {createDate: '2021-01-01 00:00:00', caseContent: '', caseResult: '', caseUniqueness: '', caseSort:'', caseDate: '', caseTime: '', visitLawyer: '', enroll: ''},
        {createDate: '2021-01-01 00:00:00', caseContent: '', caseResult: '', caseUniqueness: '', caseSort:'', caseDate: '', caseTime: '', visitLawyer: '', enroll: ''},
        {createDate: '2021-01-01 00:00:00', caseContent: '', caseResult: '', caseUniqueness: '', caseSort:'', caseDate: '', caseTime: '', visitLawyer: '', enroll: ''},
        {createDate: '2021-01-01 00:00:00', caseContent: '', caseResult: '', caseUniqueness: '', caseSort:'', caseDate: '', caseTime: '', visitLawyer: '', enroll: ''},
        {createDate: '2021-01-01 00:00:00', caseContent: '', caseResult: '', caseUniqueness: '', caseSort:'', caseDate: '', caseTime: '', visitLawyer: '', enroll: ''},
    ]);
    const [caseMemo, setCaseMemo] = useState([ // 사건메모
        {createDate: currentDate(), memoCon: ''},
        {createDate: currentDate(), memoCon: ''},
        {createDate: currentDate(), memoCon: ''},
    ]);

    const onRebaedangInput = (e, idx, targets) => {
        let copy = [...reBaedangInfo];
        copy[idx][targets] = e.target.value;
        setReBaedangInfo(copy);
    };

    const dateHandler = (date, targets, idx = 0) => {
        if (targets === "baedangDate" || targets === "endDate") {
            let copy = {...baedangInfo};
            copy[targets] = date;
            setBaedangInfo(copy);
        } else if (targets === "reBaedangDate") {
            let copy = [...reBaedangInfo];
            copy[idx][targets] = date;
            setReBaedangInfo(copy);
        } else if (targets === "caseProcess") {
            let copy = [...caseProcess];
            copy[idx]["caseDate"] = date;
            setCaseProcess(copy);
        } else if (targets === "caseMemo") {
            let copy = [...caseMemo];
            copy[idx]["createDate"] = date;
            setCaseMemo(copy);
        }
    };

    const onChageBaedang = (e, targets) => {
        let copy = {...baedangInfo};
        copy[targets] = e.target.value;
        setBaedangInfo(copy);
    };

    const onChangeCaseprocess = (e, idx, targets) => {
        let copy = [...caseProcess];
        copy[idx][targets] = e.target.value;
        setCaseProcess(copy);
    };
    
    const onChangeCaseMemoCon = (e, idx) => {
        let copy = [...caseMemo];
        copy[idx]["memoCon"] = e.target.value;
        setCaseMemo(copy);
    };

    const inputHandler1 = (e) => { 
        let {name, value} = e.target;

        if (name === "payAmount" || name === "successPay" || name === "allPay") value = parseCurrency(value);
        else if (name === "opponentPhone") value = parseOnlyPhone(value);

        setCaseInfo({
            ...caseInfo,
            [name]: value
        })
    };

    const submitHandler = () => {
        if ( checkRequired("caseRequired") ) {
            let dataToSubmit = 
                Object.assign(caseInfo, {baedangInfo: baedangInfo}, {reBaedangInfo: reBaedangInfo}, {caseProcess: caseProcess}, {caseMemo: caseMemo});
            console.log('사건진행관리정보 저장', dataToSubmit);
        }
    };

    const checkRequired = (classNames) => { // 필수값 검사 함수
        let checkEl = document.querySelectorAll(`.${classNames}`);
        for (let i = 0; i < checkEl.length; i++) {
            if (checkEl[i].value === "" && !checkEl[i].disabled) {
                let copy = {...modal};
                copy.status = true;
                copy.text = `${checkEl[i].getAttribute('data-required')}값은 필수입니다.`
                setModal(copy);
                return false;
            }
        }
        return true;
    };

    const closeModal = () => { setModal(false); }; // 모달 닫기 함수

    const changeInstituition = (targets) => { // 계속기관 select 선택 함수
        setCaseInfo({
            ...caseInfo,
            instituition: targets.value
        })
    };

    return (
        <div className="common-part mb-20 mt-20">
            <section className="part-title clearfix">
                <p className="title-txt">사건진행 관리</p>
                <p className="btn-wrapper">
                    <span 
                        className={ `default-btn-theme ml-5 ${props.PartShowAndHide.caseManage ? 'minus' : 'plus'}` }
                        onClick={()=>{props.partToggle('caseManage')}}>
                    </span>
                </p>
            </section>
            
            {
                props.PartShowAndHide.caseManage
                &&
                <section className="part-content">

                    {/* total-contract-info-table */}
                    <table className="table total-contract-info-table mb-20">
                        <tr>
                            <th rowSpan="2">총계약정보</th>
                            <th>사건기록관리번호</th>
                            <th>약정일</th>
                            <th>총약정금액</th>
                            <th>총누적입금액</th>
                            <th>총잔금</th>
                            <th>총예상성공보수</th>
                            <th>상담매니저</th>
                            <th>상담변호사</th>
                        </tr>
                        <tr>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                        </tr>
                    </table>
                    {/* // total-contract-info-table */}

                    {/* case-standard-info-table */}
                    <table className="table case-standard-info-table mb-20">
                        <tr>
                            <th rowSpan='3'>사건기본정보</th>
                            <th><i className="required"></i>사건번호</th>
                            <td>
                                <input type="text" 
                                data-required="사건번호"
                                name="caseNumber"
                                value={caseNumber}
                                onChange={inputHandler1}
                                className="common-input w-100p caseRequired"/></td>
                            <th>사건명(대)</th>
                            <td>
                                <select
                                name="caseBig"
                                value={caseBig}
                                onChange={inputHandler1}
                                className="common-input w-100p">
                                    {caseName1.map((el,idx)=> (<option value={el}>{el}</option>))}
                                </select>
                            </td>
                            <th>사건명(중)</th>
                            <td>
                                <select 
                                name="caseMiddle"
                                value={caseMiddle}
                                onChange={inputHandler1}
                                className="common-input w-100p">
                                    {caseName2.map((el,idx)=> (<option value={el}>{el}</option>))}
                                </select>
                            </td>
                            <th><i className="required"></i>사건코드번호</th>
                            <td>
                                <select 
                                name="caseCodeNumber"
                                value={caseCodeNumber}
                                onChange={inputHandler1}
                                data-required="사건코드번호"
                                className="common-input w-100p caseRequired">
                                    <option value=""></option>
                                    <option value="Ca">단행적 가처분 (Ca)</option>
                                    <option value="Cb">민사 소액 (Cb)</option>
                                    <option value="Cc">민사 조정 (Cc)</option>
                                    <option value="Cd">제1심 단독 (Cd)</option>
                                    <option value="Ce">제1심 합의부 (Ce)</option>
                                    <option value="Cf">항소심 (Cf)</option>
                                    <option value="Cg">특별사건 (Cg)</option>
                                    <option value="Ch">상고심 (Ch)</option>
                                    <option value="Ci">재심 (Ci)</option>
                                    <option value="Cj">추완항소 (Cj)</option>
                                </select>
                            </td>
                        </tr>
                        <tr>
                            <th>위임사무</th>
                            <td>{affairInfo}</td>
                            <th>사건기록관리번호</th>
                            <td>{caseManageNumber}</td>
                            <th>사건관리코드</th>
                            <td>{caseManageCode}</td>
                            <th>목표종결일</th>
                            <td>{deadLineDate}</td>
                        </tr>
                        <tr>
                            <th>계속기관</th>
                            <td className="instituition-select-box">
                                <Select 
                                value={instituitionLists.find(el => { return el.value === instituition })}
                                onChange={changeInstituition}
                                options={instituitionLists} 
                                placeholder='검색해주세요'/>
                            </td>
                            <th>계속부서</th>
                            <td><input type="text" 
                            name="instituition2"
                            value={instituition2}
                            onChange={inputHandler1}   
                            className="common-input w-100p"/></td>

                            <th><i className="required"></i>사건명</th>
                            <td colSpan="3">
                            <input type="text" 
                            name="caseDetail"
                            value={caseDetail}
                            onChange={inputHandler1}   
                            data-required="사건명"
                            className="common-input w-100p caseRequired"/></td>
                        </tr>
                        <tr>
                            <th rowSpan='2'>당사자 정보</th>
                            <th>의뢰인</th>
                            <td>{customer}</td>
                            <th>연락처</th>
                            <td>{customerPhone}</td>
                            <th>주소</th>
                            <td>{customerAddress}</td>
                            <th>이메일</th>
                            <td>{customerEmail}</td>
                        </tr>
                        <tr>
                            <th>법적지위</th>
                            <td>
                                <select 
                                name="customerRole"
                                value={customerRole}
                                onChange={inputHandler1}  
                                className="common-input w-100p">
                                    <option value=""></option>
                                    {caseRole.map((el,idx) => (<option value={el}>{el}</option>))}
                                </select>
                            </td>
                            <th>상대방</th>
                            <td>{opponent}</td>
                            <th>연락처</th>
                            <td><input type="text" 
                            name="opponentPhone"
                            value={opponentPhone}
                            onChange={inputHandler1} 
                            className="common-input w-100p"/></td>
                            <th>주소</th>
                            <td><input type="text" 
                            name="opponentAddress"
                            value={opponentAddress}
                            onChange={inputHandler1} 
                            className="common-input w-100p"/></td>
                        </tr>
                        <tr>
                            <th>계약정보</th>
                            <th>약정금액</th>
                            <td><input type="text" 
                            name="payAmount"
                            value={payAmount}
                            onChange={inputHandler1} 
                            className="common-input w-100p"/></td>
                            <th>성공보수</th>
                            <td colSpan="2"><input type="text" 
                            name="successPay"
                            value={successPay}
                            onChange={inputHandler1} 
                            className="common-input w-100p"/></td>
                            <th>총진행비용</th>
                            <td colSpan="2"><input type="text" 
                            name="allPay"
                            value={allPay}
                            onChange={inputHandler1} 
                            className="common-input w-100p"/></td>
                        </tr>
                    </table>
                    {/* // case-standard-info-table */}

                    {/* case-detail-info-table */}
                    <table className="table case-detail-info-table">
                        <tr>
                            <th rowSpan="4">배당내역 및<br/>담당자정보</th>
                            <th>배당일자</th>
                            <td><DatepickerCal dateRefresh={(date)=>dateHandler(date, "baedangDate")}/></td>
                            <th>송무담당자(직원)</th>
                            <td>
                                <select 
                                className="common-input w-100p" 
                                value={baedangInfo.songmuEmployee}
                                onChange={(e)=>{onChageBaedang(e, 'songmuEmployee')}}>
                                    <option value=""></option>
                                    {employee.map((el,idx)=> (<option value={el}>{el}</option>))}
                                </select>
                            </td>
                            <th>송무담당자(변호사)</th>
                            <td>
                                <select 
                                className="common-input w-100p"
                                value={baedangInfo.songmuLawyer}
                                onChange={(e)=>{onChageBaedang(e, 'songmuLawyer')}}>
                                    <option value=""></option>
                                    {lawyer.map((el,idx)=> (<option value={el}>{el}</option>))}
                                </select>
                            </td>
                            <th>담당팀</th>
                            <td colSpan="3">
                                <select className="common-input w-100p"
                                value={baedangInfo.team}
                                onChange={(e)=>{onChageBaedang(e, 'team')}}>
                                    <option value=""></option>
                                    {baedangTeam.map((el,idx)=> (<option value={el}>{el}</option>))}
                                </select>
                            </td>
                        </tr>
                        {
                            reBaedangInfo.map((el, idx) => (
                            <tr>
                                <th>
                                    재배당일자  
                                </th>
                                <td><DatepickerCal dateRefresh={(date)=>dateHandler(date, "reBaedangDate", idx)}/></td>
                                <th>송무담당자(직원)</th>
                                <td>
                                    <select 
                                    className="common-input w-100p"
                                    value={el.songmuEmployee}
                                    onChange={(e)=>{onRebaedangInput(e, idx, "songmuEmployee")}}>
                                        <option value=""></option>
                                        {employee.map((employee,idx)=> (<option valmaiue={employee}>{employee}</option>))}
                                    </select>
                                </td>
                                <th>송무담당자(변호사)</th>
                                <td>
                                    <select 
                                    className="common-input w-100p"
                                    value={el.songmuLawyer}
                                    onChange={(e)=>{onRebaedangInput(e, idx, "songmuLawyer")}}>
                                        <option value=""></option>
                                        {lawyer.map((lawyer,idx)=> (<option value={lawyer}>{lawyer}</option>))}
                                    </select>
                                </td>
                                <th>재배당사유</th>
                                <td colSpan="3">
                                    <select 
                                    className="common-input w-100p"
                                    value={el.reason}
                                    onChange={(e)=>{onRebaedangInput(e, idx, "reason")}}>
                                        <option value=""></option>
                                        <option value="담당자퇴사">담당자퇴사</option>
                                        <option value="전문분야">전문분야</option>
                                        <option value="의뢰인요청">의뢰인요청</option>
                                        <option value="본인역량부족">본인역량부족</option>
                                        <option value="배당착오">배당착오</option>
                                        <option value="기타">기타</option>
                                    </select>
                                </td>
                            </tr>
                            ))
                        } 
                        <tr>
                            <th>종결일자</th>
                            <td><DatepickerCal dateRefresh={(date)=>dateHandler(date, "endDate")}/></td>
                            <th>비고</th>
                            <td colSpan="7">
                                <input type="text" 
                                className="common-input w-100p"
                                value={baedangInfo.etcCon}
                                onChange={(e)=>{onChageBaedang(e, 'etcCon')}}/>
                            </td>
                        </tr>

                        {
                            caseProcess.map((el, idx) => (
                            <>
                                <tr>
                                    <th rowSpan="2">사건진행정보{idx+1}</th>
                                    <th>등록일자</th>
                                    <td>{el.createDate}</td>
                                    <th>내용</th>
                                    <td colSpan="3">
                                        <input type="text" 
                                        value={el.caseContent}
                                        onChange={(e)=>{onChangeCaseprocess(e, idx,'caseContent')}}
                                        className="common-input w-100p"/>
                                    </td>
                                    <th>결과</th>
                                    <td colSpan='3'><input type="text" 
                                        value={el.caseResult}
                                        onChange={(e)=>{onChangeCaseprocess(e, idx, 'caseResult')}}
                                        className="common-input w-100p"/>
                                    </td>
                                </tr>
                                <tr>
                                    <th>특이사항</th>
                                    <td>
                                        <select 
                                        value={el.caseUniqueness}
                                        onChange={(e)=>{onChangeCaseprocess(e, idx, 'caseUniqueness')}}
                                        className="common-input w-100p">
                                            <option value=""></option>
                                            <option value="기일변경">기일변경</option>
                                            <option value="기일불참">기일불참</option>
                                        </select>
                                    </td>
                                    <th>기일명</th>
                                    <td>
                                        <select 
                                        value={el.caseSort}
                                        onChange={(e)=>{onChangeCaseprocess(e, idx, 'caseSort')}}
                                        className="common-input w-100p">
                                            <option value=""></option>
                                            {CaseDayName.map((el,idx)=> (<option value={el}>{el}</option>))}
                                        </select>
                                    </td>
                                    <th>날짜(기일)</th>
                                    <td>
                                        <DatepickerCal dateRefresh={(date)=>dateHandler(date, "caseProcess", idx)}/><br/>
                                        <input type="time" 
                                        value={el.caseTime}
                                        onChange={(e)=>{onChangeCaseprocess(e, idx, 'caseTime')}}
                                        className="common-input w-100p mt-5" placeholder="시간입력"/>
                                    </td>
                                    <th>참석</th>
                                    <td>
                                        <select 
                                        value={el.visitLawyer}
                                        onChange={(e)=>{onChangeCaseprocess(e, idx, 'visitLawyer')}}
                                        className="common-input w-100p">
                                            <option value=""></option>
                                            {lawyer.map((el,idx)=> (<option value={el}>{el}</option>))}
                                        </select>
                                    </td>
                                    <th>등록인</th>
                                    <td>
                                        <select 
                                        value={el.enroll}
                                        onChange={(e)=>{onChangeCaseprocess(e, idx, 'enroll')}}
                                        className="common-input w-100p">
                                            <option value=""></option>
                                            {employee.map((el,idx)=> (<option value={el}>{el}</option>))}
                                        </select>
                                    </td>
                                </tr>
                            </>
                            ))
                        }

                        {
                            caseMemo.map((el, idx) => (
                                <tr>
                                    { idx === 0 && <th rowSpan="3">메모</th> }
                                    <th>일자</th>
                                    <td><DatepickerCal dateRefresh={(date)=>dateHandler(date, "caseMemo", idx)}/></td>
                                    <th>내용</th>
                                    <td colSpan='7'><input type="text" 
                                    value={el.memoCon}
                                    onChange={(e)=>{onChangeCaseMemoCon(e, idx)}}
                                    className="common-input w-100p"/></td>
                                </tr>
                            ))
                        }
                        <tr>
                            <td colSpan="11"><button className="default-btn-blue big-btn mt-5 mb-5" onClick={submitHandler}>사건진행관리 저장</button></td>
                        </tr>
                    </table>
                    {/* // case-detail-info-table */}
                </section>
            }
            {modal.status && <Modal refreshFunction={closeModal} modalText={modal.text}/>}
        </div>
    )
}

export default CaseManagePart