import React, { useState } from 'react';
import AuthChecker from './Sections/AuthChecker';
import authSettingData from '../../../Datas/authSettingData';
import styled from 'styled-components';

const BlueTh = styled.th`
    background: #deeef5 !important;
`;
const GreenTh = styled.th`
    background: #e7f5de !important;
`;

function AuthSettingPage() {
    const [authSettings, setAuthSettings] = useState(authSettingData);
    const onSubmitResults = () => {
        console.log('권한설정 값 출력', authSettings);
    };
    return (
        <div className="page-container">
            <div className="common-part">
                <section className="part-title clearfix">
                    <p className="title-txt">권한설정</p>
                    <p className="btn-wrapper">O(입력), &nbsp;V(보기), &nbsp;X(권한없음)</p>
                </section>

                <section className="part-content">
                    <table className="table auth-setting-table">
                        <tr>
                            <th></th>
                            <th></th>
                            <BlueTh>접수관리</BlueTh>
                            <BlueTh>상담관리</BlueTh>
                            <BlueTh>내사일정</BlueTh>
                            <BlueTh>결제관리</BlueTh>
                            <BlueTh>계약관리</BlueTh>
                            <BlueTh>배당관리</BlueTh>
                            <BlueTh>사건진행관리</BlueTh>
                            
                            <GreenTh>내사예약현황</GreenTh>
                            <GreenTh>매출관리</GreenTh>
                            <GreenTh>진행비용</GreenTh>
                            <GreenTh>CAPA</GreenTh>
                            <GreenTh>이현현황</GreenTh>
                            <GreenTh>환경설정</GreenTh>
                        </tr>
                        {
                            authSettings.length > 0 
                            && authSettings.map((el) => (
                                <tr key={el.authId}>
                                    <td>{el.authBelong}</td>
                                    <td>{el.authPosition}</td>
                                    <td><AuthChecker authId={el.authId} authSettings={authSettings} setAuthSettings={setAuthSettings} authNames={"authJeopsu"} authValues={el.authJeopsu}/></td>
                                    <td><AuthChecker authId={el.authId} authSettings={authSettings} setAuthSettings={setAuthSettings} authNames={"authSangdam"} authValues={el.authSangdam}/></td>
                                    <td><AuthChecker authId={el.authId} authSettings={authSettings} setAuthSettings={setAuthSettings} authNames={"authVisit"} authValues={el.authVisit}/></td>
                                    <td><AuthChecker authId={el.authId} authSettings={authSettings} setAuthSettings={setAuthSettings} authNames={"authPay"} authValues={el.authPay}/></td>
                                    <td><AuthChecker authId={el.authId} authSettings={authSettings} setAuthSettings={setAuthSettings} authNames={"authContract"} authValues={el.authContract}/></td>
                                    <td><AuthChecker authId={el.authId} authSettings={authSettings} setAuthSettings={setAuthSettings} authNames={"authBaedang"} authValues={el.authBaedang}/></td>
                                    <td><AuthChecker authId={el.authId} authSettings={authSettings} setAuthSettings={setAuthSettings} authNames={"authCaseManage"} authValues={el.authCaseManage}/></td>
                                    <td><AuthChecker authId={el.authId} authSettings={authSettings} setAuthSettings={setAuthSettings} authNames={"authVisitReserve"} authValues={el.authVisitReserve}/></td>
                                    <td><AuthChecker authId={el.authId} authSettings={authSettings} setAuthSettings={setAuthSettings} authNames={"authIncome"} authValues={el.authIncome}/></td>
                                    <td><AuthChecker authId={el.authId} authSettings={authSettings} setAuthSettings={setAuthSettings} authNames={"authExpense"} authValues={el.authExpense}/></td>
                                    <td><AuthChecker authId={el.authId} authSettings={authSettings} setAuthSettings={setAuthSettings} authNames={"authCAPA"} authValues={el.authCAPA}/></td>
                                    <td><AuthChecker authId={el.authId} authSettings={authSettings} setAuthSettings={setAuthSettings} authNames={"authDashboard"} authValues={el.authDashboard}/></td>
                                    <td><AuthChecker authId={el.authId} authSettings={authSettings} setAuthSettings={setAuthSettings} authNames={"authSetting"} authValues={el.authSetting}/></td>
                                </tr>
                            ))
                        }
                        <tr>
                            <td colSpan='15'>
                                <button
                                    onClick={onSubmitResults}
                                    className="default-btn-blue big-btn mt-20 mb-20">저장
                                </button>
                            </td> 
                        </tr>
                    </table>
                </section>

            </div>
        </div>
    )
}

export default AuthSettingPage;
