import React from 'react'
import styled from "styled-components";

function HoverText(props) {
    return (
        <HoverTextWrap mouseTop={props.MousePosition.y} mouseLeft={props.MousePosition.x}>
            {props.HoverTextContent}        
        </HoverTextWrap>
    )
}

export default HoverText

const HoverTextWrap = styled.div`
    z-index: 9999;
    position:fixed;
    background:#333;
    padding:12px;
    color:#fff;
    border-radius:5px;
    top: ${props => props.mouseTop}px;
    left:${props => props.mouseLeft}px;
`;
