import React from "react";
import { Link } from 'react-router-dom';

function MemberListPage() {

    const renderMemberLists = () => {
        const results = [];
        for(let i = 0; i < 50; i++) {
            results.push(
                <tr key={i}>
                    <td>{50-i}</td>
                    <td><Link to={`/memberList/${i}`} className='bolder'>홍길동</Link></td>
                    <td>상담마케팅</td>
                    <td>상담센터</td>
                    <td>상담1팀</td>
                    <td>상담매니저</td>
                    <td>abcdefg</td>
                    <td>상담팀원</td>
                    <td>주사무소</td>
                    <td><span className="default-btn-theme">삭제</span></td>
                </tr>
            )
        }
        return results
    }
    return (
        <div className="page-container memberlist-page">

            <div className="table-title clearfix mt-0">
                <p className="title-txt">사용자 목록</p>

                <Link to="/memberList/addMember" className="default-btn-theme">등록</Link>
            </div>  
        
            <div className="table_wrapper one_table mt-30">
                <table className="table">
                    <thead>
                        <tr>
                            <th>#</th>
                            <th>이름</th>
                            <th colSpan="3">부서</th>
                            <th>직책</th>
                            <th>아이디</th>
                            <th>관리권한</th>
                            <th>사무소</th>
                            <th>삭제</th>
                        </tr>
                    </thead>
                    <tbody>
                        {renderMemberLists()}
                    </tbody>
                </table>
            </div>
        </div>
    )
}

export default MemberListPage
