import React, { useState } from 'react';
import { creditCards } from '../../../Datas/datas';
import CardBox from './Sections/CardBox';

function CardSettingPage() {
    const [cardLists, setCardLists] = useState(creditCards);
    const [newCardName, setNewCardName] = useState('');
    const [newCardFee, setNewCardFee] = useState('');

    const submitCardHandler = () => {
        let dataToSubmit = {
            name: newCardName,
            feeRatio: newCardFee
        };
        console.log('저장할 카드 정보 출력', dataToSubmit);    
    };

    return (
        <div className="page-container">
            {/* 카드추가 start */}
            <div className="common-part mb-30">
                <section className="part-title clearfix">
                    <p className="title-txt">카드 추가</p>
                </section>
                <section className="part-content">
                    <table className="table card-plus-table">
                        <tr>
                            <th>카드명</th>
                            <th>수수료율</th>
                        </tr>
                        <tr>
                            <td><input type="text" className="common-input w-170" value={newCardName} onChange={(e)=>{setNewCardName(e.target.value)}}/></td>
                            <td><input type="text" className="common-input w-170 mr-5"value={newCardFee} onChange={(e)=>{setNewCardFee(e.target.value)}}/>%</td>
                        </tr>
                        <tr>
                            <td colSpan="2">
                                <button
                                    onClick={submitCardHandler}
                                    className="default-btn-blue big-btn mt-5 mb-5">저장
                                </button>
                            </td>
                        </tr>
                    </table>
                </section>
            </div>
            {/* 카드추가 end */}

            {/* 카드수수료 관리 start */}
            <div className="common-part">
                <section className="part-title clearfix">
                    <p className="title-txt">카드수수료 관리</p>
                </section>
                <section className="part-content">
                    <table className="table card-manage-table">
                        <tr>
                            <th>카드명</th>
                            <th>수수료율</th>
                            <th>관리</th>
                        </tr>

                        {cardLists.length > 0 && cardLists.map((el) => (
                            <CardBox key={el.cardCode} cardCode={el.cardCode} cardName={el.name} cardFee={el.feeRatio}/>
                        ))}
                    </table>
                </section>
            </div>
            {/* 카드수수료 관리 end */}

        </div>
    )
}

export default CardSettingPage
