import React, { useState } from 'react'
import { parseOnlyNumber } from '../../../utils/unitParse';
import './CaseBox.css';

// fontawesome Icon
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimes } from "@fortawesome/free-solid-svg-icons";

function CaseBox(props) {
    const [caseCode, setCaseCode] = useState(props.caseInfo.caseCode);
    const [caseMiddle, setCaseMiddle] = useState(props.caseInfo.caseMiddle);
    const [caseSortNumber, setCaseSortNumber] = useState(props.caseInfo.caseSortNumber);
    const [caseUpdateModal, setcaseUpdateModal] = useState(false);

    const caseDeleteHandler = () => {
        if (window.confirm('삭제하시겠습니까?')) {
            console.log('삭제할 사건 Id값', caseCode);
        }
    };

    const caseUpdateHandler = () => {
        if (!caseMiddle || !caseSortNumber) {
            return alert('모든 내용을 입력해주세요!');
        } 
        let dataToSubmit = {
            caseCode,
            caseMiddle,
            caseSortNumber
        };
        console.log('수정할 사건케이스 데이터 출력', dataToSubmit);
    };

    return (
       <>
            <tr>
                <td>{props.caseInfo.caseBig}</td>
                <td>{props.caseInfo.caseMiddle}</td>
                <td>{props.caseInfo.caseSortNumber}</td>
                <td>
                    <button onClick={()=>{setcaseUpdateModal(true)}} className="default-btn-blue mt-5 mb-5 mr-5">수정</button>
                    <button onClick={caseDeleteHandler} className="default-btn-red mt-5 mb-5">삭제</button>
                </td>
            </tr>

            {
                caseUpdateModal && (
                    <div className="case-update-modal">
                        <span onClick={()=>{setcaseUpdateModal(false)}} className='close-btns'><FontAwesomeIcon icon={faTimes}/></span>
                        
                        <div className='update-box'>
                            <p className="update-sect">
                                <label htmlFor="caseM">중분류</label>
                                <input type="text" className="common-input" id="caseM" value={caseMiddle} onChange={(e)=>{setCaseMiddle(e.target.value)}}/>
                            </p>
                            <p className="update-sect">
                                <label htmlFor="caseSortNum">정렬</label>
                                <input type="text" className="common-input" id="caseSortNum" value={caseSortNumber} maxLength="5" onChange={(e)=>{setCaseSortNumber(parseOnlyNumber(e.target.value))}}/>
                            </p>
                            <button onClick={caseUpdateHandler} className="default-btn-blue">수정</button>
                        </div>
                    </div>
                )
            }
       </>
    )
}



export default CaseBox
