import React, { useState } from 'react';
import { caseRole, caseName2 } from '../../../../Datas/datas';
import Modal from '../../../utils/Modal';
import { Link } from 'react-router-dom';

function LegalReportTable() {
    const [modal, setModal] = useState({ status: false, text: '' }); // 모달 state
    const closeModal = () => { setModal(false); }; // 모달 닫기 함수
    
    const [legalReportInfo, setLegalReportInfo] = useState({ // 법리검토보고서 입력 state
        caseMiddle: '', // 사건명(중)
        caseDetail: '', // 사건명(상세)
        role: '', // 법적지위
        speedLevel: '', // 급행정도
        changeLevel: '', // 변심가능성
        difficultLevel: '', // 난이도
        volumeLevel: '', // 업무량
        totalComment: '', // 종합의견
        totalFact: '', // 사실관계
        totalFact2: '', // 사건진행방향(전략)에 대한 의견
        totalLawyer: '', // 상담변호사 의견
        totalWant: '', // 의뢰인 희망사항
        totalData1: '', // 기 수령자료
        totalData2: '', // 수령 예정 자료
    }); 

    const {
        caseMiddle,
        caseDetail,
        role,
        speedLevel,
        changeLevel,
        difficultLevel,
        volumeLevel,
        totalComment,
        totalFact,
        totalFact2,
        totalLawyer,
        totalWant,
        totalData1,
        totalData2
    } = legalReportInfo;

    const inputHandler = (e) => { 
        let {name, value} = e.target;

        setLegalReportInfo({
            ...legalReportInfo,
            [name]: value
        })
    };

    const checkRequired = (classNames) => { // 필수값 검사 함수
        let checkEl = document.querySelectorAll(`.${classNames}`);
        for (let i = 0; i < checkEl.length; i++) {
            if (checkEl[i].value === "" && !checkEl[i].disabled) {
                let copy = {...modal};
                copy.status = true;
                copy.text = `${checkEl[i].getAttribute('data-required')}값은 필수입니다.`
                setModal(copy);
                return false;
            }
        }
        return true;
    };

    const submitHandler = () => {
        if ( checkRequired('caseDetail') ) {
            console.log('법리검토보고서 저장', legalReportInfo);
        }  
    }
    
    return (
        <>
            <table className='table legal-report-info-table'>
            <tr>
                <th rowSpan='10'>
                    <p>법리검토 보고서</p>    
                    <button 
                    onClick={submitHandler}
                    className="default-btn-blue big-btn mt-20 mb-20">저장</button><br/>
                    <Link target="_blank" to={`/reportLegalReview/123`} rel="noopener noreferrer" className="default-btn-theme mb-5 w-125" >법리검토보고서 출력</Link>
                </th>
                <th>사건명(중)</th>
                <td colSpan="2">
                    <select 
                    name="caseMiddle"
                    value={caseMiddle}
                    onChange={inputHandler}
                    className="common-input w-100p">
                        <option value=""></option>
                        {caseName2.map((el,idx)=>(<option key={idx} value={el}>{el}</option>))}
                    </select>
                </td>
                <th><i className="required"></i>사건명(상세)</th>
                <td colSpan="3">
                    <input type="text" 
                    name="caseDetail"
                    value={caseDetail}
                    onChange={inputHandler}
                    data-required="사건명(상세)"
                    className="common-input w-100p caseDetail"/>
                </td>
                <th>법적지위</th>
                <td colSpan="2">
                    <select 
                    name="role"
                    value={role}
                    onChange={inputHandler}
                    className="common-input w-100p">
                        <option value=""></option>
                        {caseRole.map((el,idx)=>(<option key={idx} value={el}>{el}</option>))}
                    </select>
                </td>
            </tr>
            <tr>
                <th>급행정도</th>
                <td>
                    <select 
                    name="speedLevel"
                    value={speedLevel}
                    onChange={inputHandler}
                    className="common-input w-100p">
                        <option value=""></option>
                        <option value="상">상</option>
                        <option value="중">중</option>
                        <option value="하">하</option>
                    </select>
                </td>
                <th>변심가능성</th>
                <td>
                    <select 
                    name="changeLevel"
                    value={changeLevel}
                    onChange={inputHandler}
                    className="common-input w-100p">
                        <option value=""></option>
                        <option value="상">상</option>
                        <option value="중">중</option>
                        <option value="하">하</option>
                    </select>
                </td>
                <th>난이도</th>
                <td>
                    <select 
                    name="difficultLevel"
                    value={difficultLevel}
                    onChange={inputHandler}
                    className="common-input w-100p">
                        <option value=""></option>
                        <option value="상">상</option>
                        <option value="중">중</option>
                        <option value="하">하</option>
                    </select>
                </td>
                <th>업무량</th>
                <td>
                    <select 
                        name="volumeLevel"
                        value={volumeLevel}
                        onChange={inputHandler}
                        className="common-input w-100p">
                        <option value=""></option>
                        <option value="상">상</option>
                        <option value="중">중</option>
                        <option value="하">하</option>
                    </select>
                </td>
                <td colSpan='2'></td>
            </tr>
            <tr>
                <th>종합의견</th>
                <td colSpan="9">
                    <input type="text" 
                    name="totalComment"
                    value={totalComment}
                    onChange={inputHandler}
                    className="common-input w-100p"/>
                </td>
            </tr>
            <tr>
                <th>사실관계</th>
                <td colSpan="9"><textarea 
                name="totalFact"
                value={totalFact}
                onChange={inputHandler}
                className='common-input w-100p h-120'></textarea></td>
            </tr>
            <tr>
                <th>사건진행방향(전략)에 대한 의견</th>
                <td colSpan="9"><textarea 
                name="totalFact2"
                value={totalFact2}
                onChange={inputHandler}
                className='common-input w-100p h-120'></textarea></td>
            </tr>
            <tr>
                <th>상담변호사 의견</th>
                <td colSpan="9"><textarea 
                name="totalLawyer"
                value={totalLawyer}
                onChange={inputHandler}
                className='common-input w-100p h-120'></textarea></td>
            </tr>
            <tr>
                <th>의뢰인 희망사항</th>
                <td colSpan="9"><textarea 
                name="totalWant"
                value={totalWant}
                onChange={inputHandler}
                className='common-input w-100p h-120'></textarea></td>
            </tr>
            <tr>
                <th>기 수령자료</th>
                <td colSpan="9"><textarea 
                name="totalData1"
                value={totalData1}
                onChange={inputHandler}
                className='common-input w-100p'></textarea></td>
            </tr>
            <tr>
                <th>수령 예정 자료</th>
                <td colSpan="9"><textarea 
                name="totalData2"
                value={totalData2}
                onChange={inputHandler}
                className='common-input w-100p'></textarea></td>
            </tr>
        </table>
        {modal.status && <Modal refreshFunction={closeModal} modalText={modal.text}/>}
        </>
    )
}

export default LegalReportTable
