import React, { useMemo } from "react";
import SearchBox from "../../utils/SearchBox";

function NotPayListPage() {
    const renderTable = () => {
        const results = [];
        for (let i = 0; i < 20; i++) {
            results.push(
                <tr key={i}>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                </tr>
            );
        }
        return results;
    }
    const heightStyle = useMemo(() => ({height: 'calc(100vh - 577px)'}), []);

    return (
        <div className="page-container">
            <SearchBox option={{
                dateStart: true,
                dateEnd: true,
                searchText: true
            }}/>

            <div className="table-title clearfix">
                <p className="title-txt">미수금관리</p>
            </div>  
            
            <table className="table sales_summary mb-30">
                <tr className="first-row">
                    <th colSpan='8'>미수조정사항</th>
                </tr>
                <tr>
                    <th className='border-top-none'>전일미수잔액</th>
                    <td></td>
                    <th className='border-top-none'>당일입금금액</th>
                    <td></td>
                    <th className='border-top-none'>당일미수추가금액</th>
                    <td></td>
                    <th className='border-top-none'>당일미수잔액</th>
                    <td></td>
                </tr>
            </table>

            <div className="table_wrapper" style={heightStyle}>
                <table className="table">
                    <thead>
                        <tr>
                            <th>약정서관리번호</th>
                            <th>의뢰인</th>
                            <th>수임료구분</th>
                            <th>수임일자</th>
                            <th>약정금액</th>
                            <th>분납예정일</th>
                            <th>분납예정금액</th>
                            <th>잔금총액</th>
                            <th>상담매니저</th>
                        </tr>
                    </thead>
                    <tbody>
                        {renderTable()}
                    </tbody>
                </table>
            </div>
        </div>
        
    )
}

export default NotPayListPage
