import React, { useState, useEffect } from "react";
import { Route, Switch } from "react-router-dom";
import GlobalStyles from './utils/GlobalStyles';
import { useLocation } from "react-router-dom";

// import component lists
import Footer from './views/Footer/Footer';
import Header from './views/Header/Header';
import ReceiptListPage from './views/ReceiptListPage/ReceiptListPage';
import ConsultationListPage from './views/ConsultationListPage/ConsultationListPage';
import TodayVisitListPage from './views/TodayVisitListPage/TodayVisitListPage';
import VisitListPageByPeriod from './views/VisitListPageByPeriod/VisitListPageByPeriod';
import ConfigLayoutPage from './views/ConfigLayoutPage/ConfigLayoutPage';
import LoginPage from './views/LoginPage/LoginPage';
import ContractListPage from './views/ContractListPage/ContractListPage';
import TotalSalesListPage from './views/TotalSalesListPage/TotalSalesListPage';
import DetailSalesListPage from './views/DetailSalesListPage/DetailSalesListPage';
import NotPayListPage from './views/NotPayListPage/NotPayListPage';
import RefundListPage from './views/RefundListPage/RefundListPage';
import ContractChangeListPage from './views/ContractChangeListPage/ContractChangeListPage';
import BaedangListPage from './views/BaedangListPage/BaedangListPage';
import CaseListPage from './views/CaseListPage/CaseListPage';
import SchedulerPage from './views/SchedulerPage/SchedulerPage';
import MemberListPage from './views/MemberListPage/MemberListPage';
import MemberInfoPage from './views/MemberInfoPage/MemberInfoPage';
import MainUserPage from './views/MainUserPage/MainUserPage';
import ReportVisitPage from './report/ReportVisitPage/ReportVisitPage';
import ReportAgreementPage from './report/ReportAgreementPage/ReportAgreementPage';
import ReportLegalReviewPage from './report/ReportLegalReviewPage/ReportLegalReviewPage';
import AuthSettingPage from './views/AuthSettingPage/AuthSettingPage';
import CardSettingPage from './views/CardSettingPage/CardSettingPage';
import CaseSettingPage from './views/CaseSettingPage/CaseSettingPage';
import CaseCodeSettingPage from './views/CaseCodeSettingPage/CaseCodeSettingPage';
import CallCheckPage from './views/CallCheckPage/CallCheckPage';
import SmsHistorypage from './views/SmsHistorypage/SmsHistorypage';
import AllStatisticsPage from './views/AllStatisticsPage/AllStatisticsPage';
import StatisticsByManagerPage from './views/StatisticsByManagerPage/StatisticsByManagerPage';
import InflowChartPage from './views/InflowChartPage/InflowChartPage';
import { company } from '../Datas/datas';

const useTitle = initialTitle => {
  const [Pagetitle, setPagetitle] = useState(initialTitle);
  const updateTitle = () => {
    const htmlTitle = document.querySelector('title');
    htmlTitle.innerHTML = Pagetitle;
  };
  useEffect(updateTitle, [Pagetitle]);
  return setPagetitle;
}

function App() {
  const titleUpdator = useTitle('Loading...');
  setTimeout( () => titleUpdator(`${company} 관리자페이지`), 0);
  const { pathname } = useLocation();
  return (
    <>
    <GlobalStyles />
    <div className="page-wrapper">
      {
        ['reportVisit', 'reportAgreementView', 'reportAgreementPrint', 'reportLegalReview'].indexOf(pathname.split('/')[1]) > -1
        ? 
        <Switch>
        <Route exact path={['/reportVisit', '/reportVisit/:caseCode']} component={ReportVisitPage}></Route>
        <Route exact path={['/reportAgreementView', '/reportAgreementView/:caseCode', '/reportAgreementPrint', '/reportAgreementPrint/:caseCode']} component={ReportAgreementPage}></Route>
        <Route exact path={['/reportLegalReview','/reportLegalReview/:caseCode']} component={ReportLegalReviewPage}></Route>
        </Switch>
        : ["/", "/login"].indexOf(pathname) > -1
          ? <LoginPage/>
          : (
            <>
            <Header />
            <div className="page-content">
              <Switch>
                <Route exact path="/receipt" component={ReceiptListPage}></Route>
                <Route exact path="/consultation" component={ConsultationListPage}></Route>
                <Route exact path="/visitList1" component={TodayVisitListPage}></Route>
                <Route exact path="/visitList2" component={VisitListPageByPeriod}></Route>
                <Route exact path="/pageOption" component={ConfigLayoutPage}></Route>
                <Route exact path="/contract" component={ContractListPage}></Route>
                <Route exact path="/sales" component={TotalSalesListPage}></Route>
                <Route exact path="/salesDetail" component={DetailSalesListPage}></Route>
                <Route exact path="/notpayList" component={NotPayListPage}></Route>
                <Route exact path="/refundList" component={RefundListPage}></Route>
                <Route exact path="/contractChangeList" component={ContractChangeListPage}></Route>
                <Route exact path="/baedangList" component={BaedangListPage}></Route>
                <Route exact path="/caseList" component={CaseListPage}></Route>
                <Route exact path={['/scheduler', '/scheduler/:searchByDate']} component={SchedulerPage}></Route>
                <Route exact path="/memberList" component={MemberListPage}></Route>
                <Route exact path="/memberList/:memberId" component={MemberInfoPage}></Route>
                <Route exact path={['/mainUser', '/mainUser/:caseCode']} component={MainUserPage}></Route>
                <Route exact path="/authSetting" component={AuthSettingPage}></Route>
                <Route exact path="/cardSetting" component={CardSettingPage}></Route>
                <Route exact path="/caseSetting" component={CaseSettingPage}></Route>
                <Route exact path="/caseCodeSetting" component={CaseCodeSettingPage}></Route>
                <Route exact path="/callCheck" component={CallCheckPage}></Route>
                <Route exact path="/smsSend" component={SmsHistorypage}></Route>
                <Route exact path="/allStatistics" component={AllStatisticsPage}></Route>
                <Route exact path="/statisticsByManager" component={StatisticsByManagerPage}></Route>
                <Route exact path="/inflowChart" component={InflowChartPage}></Route>
              </Switch>
            </div>
            <Footer />
            </>
        )
      }
    </div>
    </>
  );
}

export default App;
