import React, { useState, useEffect } from 'react'
import Modal from '../../../utils/Modal';
import DatepickerCal from '../../../utils/DatepickerCal';
import { creditCards } from '../../../../Datas/datas';
import { parseCurrency } from '../../../utils/unitParse';
import { currentDate } from '../../../utils/time';
import styled from 'styled-components';

// fontawesome Icon
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimes } from "@fortawesome/free-solid-svg-icons";

const ModalWrapper = styled.div`
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 500;
    width: 815px;
    background: #fff;
    padding: 30px;
    border: 1px solid #eee;
`;
const CloseButton = styled.span`
    cursor: pointer; 
    float: right;
    font-size: 22px;
    margin-top:1px;
`;

function PayUpdateModal(props) {

    const [modal, setModal] = useState({ status: false, text: '' }); // 모달 state

    const [payUpdateInfo, setPayUpdateInfo] = useState({ // 수납 상세내역 수정정보 state
        payStatus: '수임',
        payMethod: '',
        paySort: '',
        depositDate: currentDate(),
        priceAmount: '',
        depositor: '',
        howPay: '',
        cards: '',
        paper: '',
        issueNum: '',
        issueDate: currentDate(),
        isConfirm: false,
        calculateDate: currentDate(),
        calculatePrice: '',
        etcCon: ''
    }); 

    const {
        payStatus,
        payMethod,
        paySort,
        priceAmount,
        depositor,
        howPay,
        cards,
        paper,
        issueNum,
        isConfirm,
        calculatePrice,
        etcCon
    } = payUpdateInfo;

    const inputHandler = (e) => { // 수납상세내역 입력관련 함수
        let {name, value} = e.target;
        if ( ["priceAmount", "calculatePrice"].indexOf(name) > -1 ) value = parseCurrency(value);
        else if ( name == 'isConfirm' ) value = !isConfirm;
        else if (name == 'howPay') {
            if (value == '카드' ) {
                payUpdateInfo.paper = "";
                payUpdateInfo.issueNum = "";
                payUpdateInfo.issueDate = "";
            } else {
                payUpdateInfo.cards = "";
            }
        }
        setPayUpdateInfo({
            ...payUpdateInfo,
            [name]: value
        })
    };

    const checkRequired = (classNames) => { // 필수값 검사 함수
        let checkEl = document.querySelectorAll(`.${classNames}`);
        for (let i = 0; i < checkEl.length; i++) {
            if (checkEl[i].value === "" && !checkEl[i].disabled) {
                let copy = {...modal};
                copy.status = true;
                copy.text = `${checkEl[i].getAttribute('data-required')}값은 필수입니다.`
                setModal(copy);
                return false;
            }
        }
        return true;
    };

    const payDateChange = (date, types) => { 
        let copy = {...payUpdateInfo};
        copy[types] = date;
        setPayUpdateInfo(copy);
    };
    
    const submitHandler = () => {
        if ( checkRequired('required_pay_update') ) {
            console.log('수납내역 수정 데이터 출력', payUpdateInfo);
        }
    }

    const closeModal = () => { setModal(false); }; // 모달 닫기 함수

    return (
        <ModalWrapper className="common-part pay-update-modal">
             <section className="part-title clearfix">
                <p className="title-txt">수납 상세내역</p>
                <CloseButton onClick={()=>{props.setPayModal(false)}}><FontAwesomeIcon icon={faTimes} className="close-update-modal"/></CloseButton>
            </section>
            <section className="part-content">
                <table className="table pay-update-table">
                    <tr>
                        <th>수임료구분</th>
                        <td colSpan="3" className="ta-l">
                            <select 
                            name="payStatus"
                            value={payStatus}
                            onChange={inputHandler}
                            className="common-input">
                                <option value="수임">수임</option>
                                <option value="상담">상담</option>
                                <option value="추가수임">추가수임</option>
                                <option value="성공보수">성공보수</option>
                            </select>
                        </td>
                    </tr>
                    <tr>
                        <th><i className="required"></i>완납</th>
                        <td>
                            <select 
                            name="payMethod"
                            value={payMethod}
                            onChange={inputHandler}
                            data-required="완납"
                            className="common-input w-100p required_pay_update">
                                <option value=""></option>
                                <option value="예정일">예정일</option>
                                <option value="납부">납부</option>
                                <option value="취소">취소</option>
                            </select>
                        </td>
                        <th><i className="required"></i>수납구분</th>
                        <td>
                            <select 
                            name="paySort"
                            value={paySort}
                            data-required="수납구분"
                            onChange={inputHandler}
                            className="common-input w-100p required_pay_update">
                                <option value=""></option>
                                <option value="완납">완납</option>
                                <option value="분납">분납</option>
                                <option value="잔금">잔금</option>
                                <option value="선결제">선결제</option>
                                <option value="취소">취소</option>
                                <option value="재결제">재결제</option>
                            </select>
                        </td>
                    </tr>
                    <tr>
                        <th><i className="required"></i>입금날짜</th>
                        <td><DatepickerCal dateRefresh={(date)=>{payDateChange(date, "depositDate")}}/></td>
                        <th><i className="required"></i>결제금액</th>
                        <td>
                            <input type="text" 
                            name="priceAmount"
                            value={priceAmount}
                            onChange={inputHandler}
                            data-required="결제금액"
                            className="common-input w-100p required_pay_update"/>
                        </td>
                    </tr>
                    <tr>
                        <th>실입금자</th>
                        <td>
                            <input type="text" 
                            name="depositor"
                            value={depositor}
                            onChange={inputHandler}
                            className="common-input w-100p" />
                        </td>
                        <th></th>
                        <td></td>
                    </tr>
                    <tr>
                        <th><i className="required"></i>결제방식</th>
                        <td>
                            <select 
                            name="howPay"
                            value={howPay}
                            onChange={inputHandler}
                            data-required="결제방식"
                            className="common-input w-100p required_pay_update">
                                <option value=""></option>
                                <option value="카드">카드</option>
                                <option value="현금">현금</option>
                                <option value="계좌">계좌</option>
                                <option value="기타">기타</option>
                            </select>
                        </td>
                        <th><i className="required"></i>카드선택</th>
                        <td>
                            <select 
                            name="cards"
                            value={cards}
                            onChange={inputHandler}
                            data-required="카드종류"
                            disabled={howPay === "카드" ? false : true}
                            className="common-input w-100p required_pay_update">
                                <option value="">선택</option>
                                {
                                    creditCards.map((el, idx) => (
                                        <option value={el.cardCode} key={idx}>{el.name}</option>
                                    ))
                                }
                            </select>
                        </td>   
                    </tr>
                    <tr>
                        <th><i className="required"></i>증빙종류</th>
                        <td>
                            <select 
                            name="paper"
                            value={paper}
                            onChange={inputHandler}
                            data-required="증빙종류"
                            disabled={howPay === "카드"}
                            className="common-input w-100p required_pay_update">
                                <option value=""></option>
                                <option value="세금계산서">세금계산서</option>
                                <option value="현금영수증">현금영수증</option>
                                <option value="지출증빙">지출증빙</option>
                            </select>
                        </td>   
                        <th>발행번호</th>
                        <td>
                            <input type="text" 
                            name="issueNum"
                            disabled={howPay === "카드"}
                            value={issueNum}
                            onChange={inputHandler}
                            className="common-input w-100p"/>
                        </td>
                    </tr>
                    <tr>
                        <th>발행일자</th>
                        <td><DatepickerCal disabled={howPay === "카드"} dateRefresh={(date)=>{payDateChange(date, "issueDate")}}/></td>
                        <th>재무확인</th>
                        <td className='ta-l'>
                            <input type="checkbox" name="isConfirm" checked={isConfirm} onChange={inputHandler} />
                        </td>
                    </tr>
                    <tr>
                        <th>정산입금일</th>
                        <td><DatepickerCal dateRefresh={(date)=>{payDateChange(date, "calculateDate")}}/></td>
                        <th>정산입금액</th>
                        <td>
                            <input type="text" 
                            name="calculatePrice"
                            value={calculatePrice}
                            onChange={inputHandler}
                            className="common-input w-100p"/>
                        </td>
                    </tr>
                    <tr>
                        <th>비고</th>
                        <td colSpan="3">
                            <input type="text" 
                            name="etcCon"
                            value={etcCon}
                            onChange={inputHandler}
                            className="common-input w-100p"/>
                        </td>
                    </tr>
                    <tr>
                        <td colSpan='4'>
                            <button 
                            onClick={submitHandler}
                            className="default-btn-blue big-btn mt-5 mb-5">수납내역 수정</button>
                        </td>
                    </tr>
                </table>
            </section>
            {modal.status && <Modal refreshFunction={closeModal} modalText={modal.text}/>}
        </ModalWrapper>
    )
}

export default PayUpdateModal
