import React, { useState, useEffect, useMemo } from 'react';
import DatepickerCal from '../../../utils/DatepickerCal';
import { employee, lawyer, creditCards } from '../../../../Datas/datas';
import { parseCurrency } from '../../../utils/unitParse';
import Modal from '../../../utils/Modal';
import PayUpdateModal from '../Sections/PayUpdateModal';

function PaymanagePart(props) {
    
    const [modal, setModal] = useState({ status: false, text: '' }); // 모달 state
    const [Paymethod, setPaymethod] = useState(''); // 수임료완납 / 수임료분납 state
    const [payModal, setPayModal] = useState(false); // 수납상세내역 modal 상태 state

    const [depositInfo, setDepositInfo] = useState({ // 수임료완납 입력 state
        depositMoney: '',
        depositDate: '',
        howPay: '',
        cards: '',
        depositPaysort: '',
        paper: '',
        issueNum: '',
        depositEtcCon: ''
    }); 

    const {
        depositMoney,
        howPay,
        cards,
        depositPaysort,
        paper,
        issueNum,
        depositEtcCon
    } = depositInfo;

    const [standardPayInfo, setStandardPayInfo] = useState({ // 결제관리 기본 데이터 state
        contractDate: '2021-10-10',
        contractChangeDate: '',
        consultManager: '',
        consultLawyer: '',
        isNew: '',
        userName: props.userName,
        allPay: '',
        changePay: '',
        etcText: ''
    });

    const { 
        contractDate,
        contractChangeDate,
        consultManager,
        consultLawyer,
        isNew,
        userName,
        allPay,
        changePay,
        etcText
    } = standardPayInfo;

    const [DividePayInfo, setDividePayInfo] = useState([]); // 분납 입력 데이터 state

    const checkRequired = (classNames) => { // 필수값 검사 함수
        let checkEl = document.querySelectorAll(`.${classNames}`);
        for (let i = 0; i < checkEl.length; i++) {
            if (checkEl[i].value === "" && !checkEl[i].disabled) {
                let copy = {...modal};
                copy.status = true;
                copy.text = `${checkEl[i].getAttribute('data-required')}값은 필수입니다.`
                setModal(copy);
                return false;
            }
        }
        return true;
    }

    const payDefaultInputHandler = (e) => { // 결제관리 - 결제내역 기본정보입력관련 함수
        let {name, value} = e.target;
        if (name == 'allPay' || name == 'changePay') { value = parseCurrency(value); }
        setStandardPayInfo({
            ...standardPayInfo,
            [name]: value
        })
    };

    const depositHandler = (e) => { // 결제관리 - 수납입력 함수
        let {name, value} = e.target;
        if (name == 'depositMoney') {value = parseCurrency(value);}
        else if (name == 'howPay') { 
            if (value === '카드') {
                depositInfo.paper = ""; 
                depositInfo.issueNum = ""; 
            } else {
                depositInfo.cards = ""; 
            }
        } 

        setDepositInfo({
            ...depositInfo,
            [name]: value
        })
    };

    const dividePayInfoHandler = (type, index) => { // 수임료 분납입력 테이블 행 추가/삭제 함수
        if (type === 'plus') {
            let paysort = DividePayInfo.length > 0 ? DividePayInfo[0].paySort : '수임';
            setDividePayInfo([...DividePayInfo, {paySort: paysort, payAmount: '', payDate: ''}]);
        } else if (type == 'minus') {
            let copy = [...DividePayInfo];
            copy.splice(index, 1);
            setDividePayInfo(copy);
        }
    };

    const inputChangeHandler = (e, index, target) => { // 분납입력 테이블 - 분납금액, 분납일정 입력관련 함수
        let copy = [...DividePayInfo];
        if (target === "paySort") {
            copy.map((el, idx) => {
                el.paySort = e.target.value;
            });
        } else if (target === "payAmount") {
            copy[index].payAmount = parseCurrency(e.target.value);
        }
        setDividePayInfo(copy);
    };

    const payMethodFn = (e) => { // 수납 / 분납 테이블 show/hide 함수
        let payMethodSelection = '';
        if(!e) payMethodSelection = document.getElementById('pay-method-selection').value;
        else payMethodSelection = e.target.value;
        setPaymethod(payMethodSelection);
    };

    const deletePayHistory = () => { // 수납내역 삭제 함수
        if(window.confirm('삭제하시겠습니까?')) {
        }
    }

    const divideDateHandler = (date, index) => {
        let copy = [...DividePayInfo];
        copy[index].payDate = date;
        setDividePayInfo(copy);
    };

    const payDateChange = (date) => { 
        let copy = {...depositInfo};
        copy.depositDate = date;
        setDepositInfo(copy);
    };

    const submitPayDefaultInfo = (types) => {
        if ( checkRequired(types) ) {
            if (types == "pay_required_01") {
                console.log('결제내역 저장', standardPayInfo);
            } else if (types == "divide_required") {
                console.log('분납정보 저장', DividePayInfo);
            } else if (types == "allPay_required") {
                console.log('완납정보 저장', depositInfo);
            }
        }
    };

    const closeModal = () => { setModal(false); }; // 모달 닫기 함수
    
    useEffect(() => {
        payMethodFn();
    }, []);

    const borderTopGrey = useMemo(() => ({borderTop: '1px solid #ccc'}), []);
    const mw_200 = useMemo(() => ({minWidth: '200px'}), []);
    const colorRed = useMemo(() => ({color: '#f30'}), []);
    const widthDetail = useMemo(() => ({width: '261.16px'}), []);
    const greyFont = useMemo(() => ({color: '#999'}), []);
    const widthCalc = useMemo(() => ({width: 'calc(100% - 90px)'}), []);
    const widthCalc2 = useMemo(() => ({width: 'calc(50% - 5px)'}), []);
    const btnStyles = useMemo(() => ({position: 'absolute', right: '10px', top:'6px'}), []);
    
    return (
        <div className="common-part mb-20 mt-20">
            <section className="part-title clearfix">
                <p className="title-txt">결제관리</p>
                <p className="btn-wrapper">
                    <span 
                        className={ `default-btn-theme ml-5 ${props.PartShowAndHide.paymanage ? 'minus' : 'plus'}` }
                        onClick={()=>{props.partToggle('paymanage')}}>
                    </span>
                </p>
            </section>
            
            {
                props.PartShowAndHide.paymanage
                &&
                    <section className="part-content">
                        {/* standard-payment-info-table */}
                        <table className="table standard-payment-info-table mb-20">
                            <tr>
                                <th>계약일자</th>
                                <td colSpan="4" className='ta-l'>{contractDate}</td>
                                <th>약정변경일</th>
                                <td colSpan="3" className='ta-l'>{contractChangeDate}</td>
                            </tr>
                            <tr>
                                <th rowSpan="3">결제내역</th>
                                <th>상담매니저</th>
                                <td colSpan="3">
                                    <select className="common-input w-100p" name="consultManager" value={consultManager} onChange={payDefaultInputHandler}>
                                        <option value=""></option>
                                        {employee.map((el,idx)=>(<option key={idx} value={el}>{el}</option>))}
                                    </select>
                                </td>
                                <th>상담변호사</th>
                                <td>
                                    <select className="common-input w-100p" name="consultLawyer" value={consultLawyer} onChange={payDefaultInputHandler}>
                                        <option value=""></option>
                                        {lawyer.map((el,idx)=>(<option key={idx} value={el}>{el}</option>))}
                                    </select>
                                </td>
                                <th>신규여부</th>
                                <td>
                                    <select className="common-input w-100p" name="isNew" value={isNew} onChange={payDefaultInputHandler}>
                                        <option value="신규">신규</option>
                                        <option value="기존">기존</option>
                                    </select>
                                </td>
                            </tr>
                            <tr>
                                <th>의뢰인</th>
                                <td>{userName}</td>
                                <th><i className="required"></i>약정금액</th>
                                <td>
                                    <input type="text" 
                                    name="allPay"
                                    value={allPay}
                                    onChange={payDefaultInputHandler}
                                    className="common-input w-100p mb-5 pay_required_01" 
                                    data-required="약정금액"/>
                                    <p style={greyFont}>* 수임, 추가수임 선택 시에만 약정금액 작성바랍니다.</p>
                                </td>
                                <th>약정변경</th>
                                <td>
                                    <input type="text" 
                                    name="changePay"
                                    value={changePay}
                                    onChange={payDefaultInputHandler}
                                    className="common-input w-100p mb-5"/>
                                </td>
                                <th rowSpan='2'><i className="required"></i>약정금 납부구분</th>
                                <td rowSpan='2'>
                                    <select 
                                    data-required="약정금 납부구분"
                                    className="common-input w-100p pay_required_01" 
                                    id="pay-method-selection" 
                                    onChange={payMethodFn}>
                                        <option value=""></option>
                                        <option value="수임료완납" selected>수임료완납</option>
                                        <option value="수임료분납">수임료분납</option>
                                    </select>
                                </td>
                            </tr>
                            <tr>
                                <th>비고</th>
                                <td colSpan='5'>
                                    <input 
                                        name="etcText"
                                        value={etcText}
                                        onChange={payDefaultInputHandler}
                                        type="text" 
                                        className="common-input" 
                                        style={widthCalc}/>
                                    <button 
                                    onClick={()=>{submitPayDefaultInfo('pay_required_01')}}
                                    className="default-btn-blue ml-5">결제내역저장</button>
                                </td>
                            </tr>
                        </table>
                        {/* // standard-payment-info-table */}
                        
                        {
                            Paymethod === '수임료완납'
                            &&  
                                /* '수임료완납' 선택시 수납입력 table show */
                                <table className="table pay-at-once-table">
                                    <tr><th colSpan='6'>수납 입력</th></tr>
                                    <tr>
                                        <th><i className="required"></i>입금금액</th>
                                        <td>
                                            <input type="text" 
                                            data-required="입금금액"
                                            name="depositMoney"
                                            value={depositMoney}
                                            onChange={depositHandler}
                                            className="common-input w-100p allPay_required"/>
                                        </td>
                                        <th><i className="required"></i>입금일</th>
                                        <td><DatepickerCal dateRefresh={payDateChange}/></td>
                                        <th><i className="required"></i>결제방식</th>
                                        <td>
                                            <select 
                                            data-required="결제방식"
                                            name="howPay"
                                            value={howPay}
                                            onChange={depositHandler}
                                            className="common-input mr-5 allPay_required" 
                                            style={widthCalc2}>
                                                <option value=""></option>
                                                <option value="카드">카드</option>
                                                <option value="현금">현금</option>
                                                <option value="계좌">계좌</option>
                                                <option value="기타">기타</option>
                                            </select>
                                            <select 
                                            data-required="카드종류"
                                            name="cards"
                                            value={cards}
                                            onChange={depositHandler}
                                            disabled={howPay === "카드" ? false : true}
                                            className="common-input ml-5 allPay_required" 
                                            style={widthCalc2}>
                                                <option value="">선택</option>
                                                {
                                                    creditCards.map((el, idx) => (
                                                        <option value={el.cardCode} key={idx}>{el.name}</option>
                                                    ))
                                                }
                                            </select>
                                        </td>
                                    </tr>
                                    <tr>
                                        <th><i className="required"></i>수임료 구분</th>
                                        <td>
                                            <select 
                                            data-required="수임료구분"
                                            name="depositPaysort"
                                            value={depositPaysort}
                                            onChange={depositHandler}
                                            className="common-input w-100p allPay_required">
                                                <option value=""></option>
                                                <option value="수임">수임</option>
                                                <option value="상담">상담</option>
                                                <option value="추가수임">추가수임</option>
                                                <option value="성공보수">성공보수</option>
                                            </select>
                                        </td>
                                        <th><i className="required"></i>증빙종류</th>
                                        <td>
                                            <select 
                                            data-required="증빙종류"
                                            name="paper"
                                            value={paper}
                                            onChange={depositHandler}
                                            disabled={howPay === "카드"}
                                            className="common-input w-100p allPay_required">
                                                <option value=""></option>
                                                <option value="세금계산서">세금계산서</option>
                                                <option value="현금영수증">현금영수증</option>
                                                <option value="지출증빙">지출증빙</option>
                                            </select>
                                        </td>
                                        <th>발행번호</th>
                                        <td>
                                            <input type="text" 
                                            disabled={howPay === "카드"}
                                            name="issueNum"
                                            value={issueNum}
                                            onChange={depositHandler}
                                            className="common-input w-100p"/>
                                        </td>
                                    </tr>
                                    <tr>
                                        <th>비고</th>
                                        <td colSpan='5'>
                                            <input type="text" 
                                            name="depositEtcCon"
                                            value={depositEtcCon}
                                            onChange={depositHandler}
                                            className='common-input w-100p'/>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td colSpan='6'>
                                            <button 
                                            onClick={()=>{submitPayDefaultInfo('allPay_required')}}
                                            className="default-btn-blue big-btn mt-5 mb-5">입금정보 저장</button>
                                        </td>
                                    </tr>
                                </table>
                                /* // '수임료완납'선택시 수납입력 table show */
                        }
                        {
                            Paymethod === '수임료분납'
                            &&
                                /* '수임료분납' 선택시 수납입력 table show */
                                <table className="table pay-divide-table ">
                                    <tr>
                                        <th colSpan='6' className='p-r'>
                                            분납 입력
                                            <span className="default-btn-green" 
                                            style={btnStyles}
                                            onClick={()=>{dividePayInfoHandler('plus')}}
                                            >추가</span>
                                        </th>
                                    </tr>
                                    {
                                        DividePayInfo.map((el, idx) => (
                                            <tr key={idx}>  
                                                <th>
                                                    {idx === 0 && '수임료 구분'}
                                                </th>
                                                <td>
                                                    {
                                                        idx === 0 
                                                        && 
                                                        (  <select 
                                                            className="common-input w-100p" 
                                                            value={el.paySort} 
                                                            onChange={(e)=>{inputChangeHandler(e, idx, "paySort")}} >
                                                                <option value="수임">수임</option>
                                                                <option value="상담">상담</option>
                                                                <option value="추가수임">추가수임</option>
                                                                <option value="성공보수">성공보수</option>
                                                            </select>
                                                        )
                                                    }
                                                
                                                </td>
                                                <th><i className="required"></i>분납금액</th>
                                                <td>
                                                    <input type="text" 
                                                    className="common-input w-100p divide_required" 
                                                    data-required="분납금액"
                                                    value={el.payAmount}
                                                    onChange={(e)=>{inputChangeHandler(e, idx, "payAmount")}}/>
                                                </td>
                                                <th><i className="required"></i>분납일정</th>
                                                <td className="flex-con m-0">
                                                    <DatepickerCal dateRefresh={(date)=>{divideDateHandler(date, idx)}} /> 
                                                    <span 
                                                        className="default-btn-red ml-5" 
                                                        onClick={()=>{dividePayInfoHandler('minus', idx)}}
                                                    >삭제</span>
                                                </td>
                                            </tr>
                                        ))
                                    }
                                    <tr>
                                        <td colSpan='6'>
                                            <button 
                                            onClick={()=>{submitPayDefaultInfo('divide_required')}}
                                            className="default-btn-blue big-btn mt-5 mb-5">분납정보 저장</button></td>
                                    </tr>
                                </table>
                                /* // '수임료분납'선택시 수납입력 table show */
                        }

                        {/* pay-history-table */}
                        <table className="table pay-history-table mt-20">
                            <tr><th colSpan="14">수납내역</th></tr>
                            <tr>
                                <th>수임료구분</th>
                                <th>수납구분</th>
                                <th>분납예정일</th>
                                <th>분납금액</th>
                                <th>입금날짜</th>
                                <th>입금액</th>
                                <th>결제방식</th>
                                <th>증빙</th>
                                <th>발행번호</th>
                                <th>정산입금일</th>
                                <th>정산입금액</th>
                                <th>비고</th>
                                <th>납부여부</th>
                                <th>-</th>
                            </tr>
                                <tr>
                                <td>수임</td>
                                <td>잔금</td>
                                <td>2021-01-01</td>
                                <td>500,000</td>
                                <td>2021-01-01</td>
                                <td>0</td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td>X</td>
                                <td>
                                    <span onClick={()=>{setPayModal(true)}} className="default-btn-green mr-5">수정</span>
                                    <span className="default-btn-red" onClick={deletePayHistory}>삭제</span>
                                </td>
                                </tr>
                            <tr>
                                <th>합계</th>
                                <th colSpan='2'>입금액</th>
                                <td colSpan='2'></td>
                                <th>공급가액</th>
                                <td colSpan='2'></td>
                                <th>부가세</th>
                                <td colSpan='2'></td>
                                <th>정산입금액</th>
                                <td colSpan='2' style={colorRed}></td>
                            </tr>
                        </table>
                        { payModal && <PayUpdateModal setPayModal={setPayModal}/> }
                        
                        {/* // pay-history-table */}

                        <table className="table mt-20">
                            <th style={widthDetail}>미수금 관리 상태</th>
                            <td className="ta-l" style={borderTopGrey} colSpan="5">
                                <select className="common-input" style={mw_200}>
                                    <option value=""></option>
                                    <option value="내용증명">내용증명</option>
                                    <option value="처리완료">처리완료</option>
                                    <option value="소송중">소송중</option>
                                    <option value="미결">미결</option>
                                    <option value="기타">기타</option>
                                </select>
                            </td>
                        </table>
                    </section>
            } 
            {modal.status && <Modal refreshFunction={closeModal} modalText={modal.text}/>}

        </div>
    )
}

export default PaymanagePart
