import React, { useState } from "react";
import SearchBox from "../../utils/SearchBox";
import articleLists from '../../../Datas/articleLists4';

function ContractListPage() {
  const [Article, setArticle] = useState(articleLists);

  return (
    <div className="page-container">
      <SearchBox option={{
        dateStart: true,
        dateEnd: true,
        route: true,
        isNew: true,
        consultResult1: true,
        consultResult2: true,
        jeopsuEmployee: true,
        searchText: true
      }}/>

      <div className="table-title clearfix">
        <p className="title-txt">계약관리</p>

        <a href="javascript:;" className="default-btn">엑셀다운</a>
      </div>

      <div className="table_wrapper">
        <table className="table">
          <thead>
            <tr>
              <th>상담결과</th>
              <th>수임일자</th>
              <th>신규여부</th>
              <th>이름</th>
              <th>연락처</th>
              <th>사건명(중)</th>
              <th>법적지위</th>
              <th>약정금액</th>
              <th>약정서</th>
              <th>첨부파일</th>
              <th>법리검토보고서</th>
              <th>상담매니저</th>
              <th>상담변호사</th>
            </tr>
          </thead>
          <tbody>

       
            {
                Article.map((el,idx)=>(
                    <tr key={idx}>
                      <td>{el.visitConsultationResult}</td>
                      <td>{el.contractDate}</td>
                      <td>{el.isNew}</td>
                      <td>{el.name}</td>
                      <td>{el.phone}</td>
                      <td>{el.caseName2}</td>
                      <td>{el.caseRole}</td>
                      <td>{el.contractMoney}</td>
                      <td><span className="default-btn-theme">내용</span></td>
                      <td><span className="default-btn-theme">내용</span></td>
                      <td><span className="default-btn-theme">내용</span></td>
                      <td>{el.consultManager}</td>
                      <td>{el.consultLawyer1}</td>
                    </tr>
                ))
            }
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default ContractListPage;
