import React, { useState } from 'react'
import { parseOnlyNumber } from '../../../utils/unitParse';

// fontawesome Icon
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimes } from "@fortawesome/free-solid-svg-icons";

function CaseCodeBox(props) {
    const [caseCodeKeyV, setCaseCodeKeyV] = useState(props.caseCodeInfo.caseCodeKeyV);
    const [caseCodeSort, setCaseCodeSort] = useState(props.caseCodeInfo.caseCodeSort);
    const [caseCodeV, setCaseCodeV] = useState(props.caseCodeInfo.caseCodeV);
    const [expectedMonth, setExpectedMonth] = useState(props.caseCodeInfo.expectedMonth);
    const [sortNumber, setSortNumber] = useState(props.caseCodeInfo.sortNumber);
    const [caseCodeUpdateModal, setCaseCodeUpdateModal] = useState(false);

    const caseCodeDeleteHandler = () => {
        if (window.confirm('삭제하시겠습니까?')) {
            console.log('삭제할 사건코드 Id값', caseCodeKeyV);
        }
    };
    
    const caseCodeUpdateHandler = () => {
        if (!caseCodeSort || !caseCodeV || !expectedMonth || !sortNumber ) {
            return alert('모든 내용을 입력해주세요!');
        }
        let dataToSubmit = {
            caseCodeKeyV,
            caseCodeSort,
            caseCodeV,
            expectedMonth,
            sortNumber
        };
        console.log('수정할 사건케이스 데이터 출력', dataToSubmit);
    };

    return (
        <>
            <tr>
                <td>{props.caseCodeInfo.caseCodeBig}</td>
                <td>{props.caseCodeInfo.caseCodeSort}</td>
                <td>{props.caseCodeInfo.caseCodeV}</td>
                <td>{props.caseCodeInfo.expectedMonth}</td>
                <td>{props.caseCodeInfo.sortNumber}</td>
                <td>
                    <button onClick={()=>{setCaseCodeUpdateModal(true)}} className="default-btn-blue mt-5 mb-5 mr-5">수정</button>
                    <button onClick={caseCodeDeleteHandler} className="default-btn-red mt-5 mb-5">삭제</button>
                </td>
            </tr>

            {
                caseCodeUpdateModal && (
                    <div className="case-update-modal">
                        <span onClick={()=>{setCaseCodeUpdateModal(false)}} className='close-btns'><FontAwesomeIcon icon={faTimes}/></span>
                        
                        <div className='update-box'>
                            <p className="update-sect">
                                <label htmlFor="casecodevalue1">절차분류</label>
                                <input type="text" className="common-input" id="casecodevalue1" value={caseCodeSort} onChange={(e)=>{setCaseCodeSort(e.target.value)}}/>
                            </p>
                            <p className="update-sect">
                                <label htmlFor="casecodevalue2">코드</label>
                                <input type="text" className="common-input" id="casecodevalue2" value={caseCodeV} onChange={(e)=>{setCaseCodeV(e.target.value)}}/>
                            </p>
                            <p className="update-sect">
                                <label htmlFor="casecodevalue3">예상종결(개월)</label>
                                <input type="text" className="common-input" id="casecodevalue3" value={expectedMonth} maxLength='4' onChange={(e)=>{setExpectedMonth(parseOnlyNumber(e.target.value))}}/>
                            </p>
                            <p className="update-sect">
                                <label htmlFor="casecodevalue4">정렬</label>
                                <input type="text" className="common-input" id="casecodevalue4" value={sortNumber} maxLength="5" onChange={(e)=>{setSortNumber(parseOnlyNumber(e.target.value))}}/>
                            </p>
                            <button onClick={caseCodeUpdateHandler} className="default-btn-blue">수정</button>
                        </div>
                    </div>
                )
            }
       </>
    )
}

export default CaseCodeBox
