import React, {useMemo} from 'react'

function InflowByConsultant(props) {

    const greenBg = useMemo(() => ({background: '#beb'}), []);
    const blueBg = useMemo(() => ({background: '#ddf'}), []);
    const separateBorder = useMemo(() => ({borderTop: '2px solid #000', borderBottom: "2px solid #000"}), []);

    return (
        <table className='table mb-15' style={separateBorder}>
            <tr>
                <th>이름</th>
                <th>결과</th>
                <th>경로없음</th>
                <th>고객소개</th>
                <th>구글키워드</th>
                <th>기타</th>
                <th>다음키워드</th>
                <th>블로그1</th>
                <th>블로그2</th>
                <th>워크인</th>
                <th>직원소개</th>
                <th>추가수임</th>
                <th>카카오톡</th>
                <th>홈페이지</th>
                <th>합계</th>
            </tr>
            <tr>
                <th rowSpan={3}>{props.dataValues}</th>
                <th>내사수</th>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
            </tr>
            <tr>
                <th>계약수</th>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
            </tr>
            <tr>
                <th>DB수</th>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
            </tr>
            <tr>
                <th colSpan={2}>내사율</th>
                <td style={greenBg}></td>
                <td style={greenBg}></td>
                <td style={greenBg}></td>
                <td style={greenBg}></td>
                <td style={greenBg}></td>
                <td style={greenBg}></td>
                <td style={greenBg}></td>
                <td style={greenBg}></td>
                <td style={greenBg}></td>
                <td style={greenBg}></td>
                <td style={greenBg}></td>
                <td style={greenBg}></td>
                <td style={greenBg}></td>
            </tr>
            <tr>
                <th colSpan={2}>계약율</th>
                <td style={blueBg}></td>
                <td style={blueBg}></td>
                <td style={blueBg}></td>
                <td style={blueBg}></td>
                <td style={blueBg}></td>
                <td style={blueBg}></td>
                <td style={blueBg}></td>
                <td style={blueBg}></td>
                <td style={blueBg}></td>
                <td style={blueBg}></td>
                <td style={blueBg}></td>
                <td style={blueBg}></td>
                <td style={blueBg}></td>
            </tr>
        </table>
    )
}

export default InflowByConsultant
