import React, { useState, useMemo } from 'react';
import { caseName1, caseName2 } from '../../../../Datas/datas';
import Modal from '../../../utils/Modal';
import LegalReportTable from './LegalReportTable';
import { Link } from 'react-router-dom';
import {
    parseOnlyPhone,
    parseCurrency
} from '../../../utils/unitParse';
import styled from 'styled-components';

const CheckLists = styled.p`
    float: left;
    width: 25%;
    margin-top:5px;
`

function ContractManagePart(props) {
    const [modal, setModal] = useState({ status: false, text: '' }); // 모달 state
    const [trialCost, setTrialCost] = useState([]); // 재판비용 state

    const checkToggle = (value) => { // 재판비용 checkbox 함수
        let currentIdx = trialCost.indexOf(value);
        let copy = [...trialCost];
        if (currentIdx === -1) copy.push(value);
        else copy.splice(currentIdx, 1);
        setTrialCost(copy);
    }

    const [clients, setClients] = useState([ // 의뢰인 state
        {clientsName: '', clientsPhone: '', clientsJumin: '', clientsAddress: ''}
    ]); 

    const clientsInfoRowH = (types, index) => { // 의뢰인 행 추가/삭제 함수
        let copy = [...clients];
        if (types === 'plus') {
            copy.push({clientsName: '', clientsPhone: '', clientsJumin: '', clientsAddress: ''});
            setClients(copy);
        } else {
            copy.splice(index, 1);
            setClients(copy);
        }
    };

    const changeClientInfo = (e, index, target) => { // 의뢰인 정보 입력관련 함수
        let copy = [...clients];
        if (target === "clientsPhone" || target === "clientsJumin" ) copy[index][target] = parseOnlyPhone(e.target.value);
        else copy[index][target] = e.target.value;
        setClients(copy);
    };

    const [deputys, setDeputys] = useState([ // 대리인 state
        {deputysName: '', deputysPhone: '', deputysJumin: '', deputysAddress: '', deputysRelation: ''}
    ]); 

    const deputysInfoRowH = (types, index) => { // 대리인 행 추가/삭제 함수
        let copy = [...deputys];
        if (types === 'plus') {
            copy.push({deputysName: '', deputysPhone: '', deputysJumin: '', deputysAddress: '', deputysRelation: ''});
            setDeputys(copy);
        } else {
            copy.splice(index, 1);
            setDeputys(copy);
        }
    };

    const changeDeputysInfo = (e, index, target) => { // 대리인 정보 입력관련 함수
        let copy = [...deputys];
        if (target === "deputysPhone" || target === "deputysJumin" ) copy[index][target] = parseOnlyPhone(e.target.value);
        else copy[index][target] = e.target.value;
        setDeputys(copy);
    };

    const [otherPay, setOtherPay] = useState([ // 기타보수 state
        {articles: '', articlesMoney: ''}
    ]); 

    const otherPayRowH = (types, index) => { // 대리인 행 추가/삭제 함수
        let copy = [...otherPay];
        if (types === 'plus') {
            copy.push({articles: '', articlesMoney: ''});
            setOtherPay(copy);
        } else {
            copy.splice(index, 1);
            setOtherPay(copy);
        }
    };

    const changeOtherPay = (e, index, target) => { // 대리인 정보 입력관련 함수
        let copy = [...otherPay];
        if (target === "articlesMoney" ) copy[index][target] = parseCurrency(e.target.value);
        else copy[index][target] = e.target.value;
        setOtherPay(copy);
    };

    const [contractInfo, setContractInfo] = useState({ // 계약정보 입력 state
        caseBig: '', // 사건명(대)
        caseMiddle: '', // 사건명(중)
        opponent: '', // 상대방
        affairInfo1: '', // 위임사무
        affairInfo2: '전체', // 위임보수 적용사무
        affairInfo3: '', // 위임보수 적용사무 - '일부' 선택시 추가적으로 넣어야하는 값
        installmentSchedule: '', // 분납일정
        successPay1: '', // 성공보수 유무
        successPay2: '', // 성공보수 지급요건
        successVAT: '', // 부가세
        successPay3: '', // 성공보수 예상금액
        etcCon: '', // 특양사항
    }); 

    const {
        caseBig,
        caseMiddle,
        opponent,
        affairInfo1,
        affairInfo2,
        affairInfo3,
        installmentSchedule,
        successPay1,
        successPay2,
        successVAT,
        successPay3,
        etcCon
    } = contractInfo;
    
    const contractInputHandler = (e) => { 
        let {name, value} = e.target;

        if (name == 'affairInfo2') {
            if (value == '전체') contractInfo.affairInfo3 = "";
        } else if (name == 'successPay3') {
            value = parseCurrency(value);
        }

        setContractInfo({
            ...contractInfo,
            [name]: value
        })
    };

    const checkRequired = (classNames) => { // 필수값 검사 함수
        let checkEl = document.querySelectorAll(`.${classNames}`);
        for (let i = 0; i < checkEl.length; i++) {
            if (checkEl[i].value === "" && !checkEl[i].disabled) {
                let copy = {...modal};
                copy.status = true;
                copy.text = `${checkEl[i].getAttribute('data-required')}값은 필수입니다.`
                setModal(copy);
                return false;
            }
        }
        return true;
    };

    const submitContractInfo = (classNames) => { // 계약정보 저장 함수
        if ( checkRequired(classNames) ) {
            let dataToSubmit = Object.assign(contractInfo, {clients: clients}, {deputys: deputys}, {otherPay: otherPay} ); 
            console.log('계약관리정보 저장', dataToSubmit);
        }
    };

    const closeModal = () => { setModal(false); }; // 모달 닫기 함수

    const buttonStyle = useMemo(() => ({fontSize: 20, verticalAlign: 'middle'}), []);

    return (
        <div className="common-part mb-20 mt-20">
            <section className="part-title clearfix">
                <p className="title-txt">계약관리</p>
                <p className="btn-wrapper">
                    <span 
                        className={ `default-btn-theme ml-5 ${props.PartShowAndHide.contractManage ? 'minus' : 'plus'}` }
                        onClick={()=>{props.partToggle('contractManage')}}>
                    </span>
                </p>
            </section>
            
            {
                props.PartShowAndHide.contractManage
                &&
                    <section className="part-content">

                        {/* contract-info-table */}
                        <table className='table contract-info-table'>
                            <tr>
                                <th rowSpan={7 + clients.length + deputys.length}>
                                    <p>계약정보</p>    
                                    <button 
                                    onClick={()=>{submitContractInfo('contract_required')}}
                                    className="default-btn-blue big-btn mt-20 mb-20">저장</button><br/>
                                    <Link target="_blank" to={`/reportAgreementPrint/123`} rel="noopener noreferrer" className="default-btn-theme mb-5">약정서 출력</Link><br/>
                                    <Link target="_blank" to={`/reportAgreementView/123`} rel="noopener noreferrer" className="default-btn-theme">약정서 보기</Link>
                                </th>
                                <th><i className="required"></i>사건명(대)</th>
                                <td>
                                    <select 
                                    data-required="사건명(대)"
                                    name="caseBig"
                                    value={caseBig}
                                    onChange={contractInputHandler}
                                    className="common-input w-100p contract_required">
                                        <option value=""></option>
                                        {caseName1.map((el,idx)=>(<option key={idx} value={el}>{el}</option>))}
                                    </select>
                                </td>
                                <th><i className="required"></i>사건명(중)</th>
                                <td>
                                    <select
                                    data-required="사건명(중)"
                                    name="caseMiddle"
                                    value={caseMiddle}
                                    onChange={contractInputHandler}
                                    className="common-input w-100p contract_required">
                                        <option value=""></option>
                                        {caseName2.map((el,idx)=>(<option key={idx} value={el}>{el}</option>))}
                                    </select>
                                </td>
                                <th>상대방</th>
                                <td colSpan="5">
                                    <input type="text" 
                                    name="opponent"
                                    value={opponent}
                                    onChange={contractInputHandler}
                                    className="common-input w-100p"/>
                                </td>
                            </tr>   

                            {
                                clients.map((el, idx) => (
                                    <tr key={idx}>
                                        <th><i className="required"></i>의뢰인 
                                            {
                                                idx === 0 
                                                ? <button onClick={()=>{clientsInfoRowH('plus', idx)}} className="default-btn-green ml-5">추가</button>
                                                : <button onClick={()=>{clientsInfoRowH('minus', idx)}} className="default-btn-red ml-5">삭제</button>
                                            }
                                        </th>
                                        <td>
                                            <input type="text" 
                                            data-required="의뢰인"
                                            value={el.clientsName}
                                            onChange={(e)=>{changeClientInfo(e, idx, "clientsName")}}
                                            className="common-input w-100p contract_required"/>
                                        </td>
                                        <th><i className="required"></i>의뢰인 연락처</th>
                                        <td>
                                            <input type="text" 
                                            data-required="의뢰인 연락처"
                                            value={el.clientsPhone}
                                            onChange={(e)=>{changeClientInfo(e, idx, "clientsPhone")}}
                                            className="common-input w-100p contract_required"/>
                                        </td>
                                        <th><i className="required"></i>의뢰인 주민번호</th>
                                        <td>
                                            <input type="text" 
                                            value={el.clientsJumin}
                                            onChange={(e)=>{changeClientInfo(e, idx, "clientsJumin")}}
                                            data-required="의뢰인 주민번호"
                                            className="common-input w-100p contract_required"/>
                                        </td>
                                        <th><i className="required"></i>의뢰인 주소</th>
                                        <td colSpan='3'>
                                            <input type="text" 
                                            value={el.clientsAddress}
                                            onChange={(e)=>{changeClientInfo(e, idx, "clientsAddress")}}
                                            data-required="의뢰인 주소"
                                            className="common-input w-100p contract_required"/>
                                        </td>
                                    </tr>     
                                ))
                            }

                            {
                                deputys.map((el, idx) => (
                                    <tr key={idx}>
                                        <th>
                                            <i className="required"></i>대리인 
                                            {
                                                idx === 0 
                                                ? <button onClick={()=>{deputysInfoRowH('plus', idx)}} className="default-btn-green ml-5">추가</button>
                                                : <button onClick={()=>{deputysInfoRowH('minus', idx)}} className="default-btn-red ml-5">삭제</button>
                                            }
                                        </th>
                                        <td>
                                            <input type="text" 
                                            data-required="대리인"
                                            value={el.deputysName}
                                            onChange={(e)=>{changeDeputysInfo(e, idx, "deputysName")}}
                                            className="common-input w-100p contract_required"/>
                                        </td>
                                        <th><i className="required"></i>대리인 연락처</th>
                                        <td>
                                            <input type="text" 
                                            data-required="대리인 연락처"
                                            value={el.deputysPhone}
                                            onChange={(e)=>{changeDeputysInfo(e, idx, "deputysPhone")}}
                                            className="common-input w-100p contract_required"/>
                                        </td>
                                        <th><i className="required"></i>대리인 주민번호</th>
                                        <td>
                                            <input type="text" 
                                            data-required="대리인 주민번호"
                                            value={el.deputysJumin}
                                            onChange={(e)=>{changeDeputysInfo(e, idx, "deputysJumin")}}
                                            className="common-input w-100p contract_required"/>
                                        </td>
                                        <th><i className="required"></i>대리인 주소</th>
                                        <td>
                                            <input type="text" 
                                            data-required="대리인 주소"
                                            value={el.deputysAddress}
                                            onChange={(e)=>{changeDeputysInfo(e, idx, "deputysAddress")}}
                                            className="common-input w-100p contract_required"/>
                                        </td>
                                        <th>의뢰인과의 관계</th>
                                        <td>
                                            <input type="text" 
                                            value={el.deputysRelation}
                                            onChange={(e)=>{changeDeputysInfo(e, idx, "deputysRelation")}}
                                            className="common-input w-100p"/>
                                        </td>
                                    </tr>   
                                ))
                            }
                            <tr>
                                <th><i className='required'></i>위임사무</th>
                                <td colSpan="4">
                                    <textarea 
                                    data-required="위임사무"
                                    name="affairInfo1"
                                    value={affairInfo1}
                                    onChange={contractInputHandler}
                                    className='common-input w-100p contract_required'></textarea>
                                </td>
                                <th><i className="required"></i>위임보수 적용사무</th>
                                <td colSpan="4" className='ta-l'>
                                    <span className='units-wrap'>
                                        <input 
                                            type="radio" 
                                            name='affairInfo2' 
                                            value='전체' 
                                            id="affairInfo2_1"
                                            onChange={contractInputHandler} 
                                            checked={affairInfo2 === "전체"}
                                        />
                                        <label htmlFor="affairInfo2_1" className="ml-5 mr-10">전체</label>
                                        <input 
                                            type="radio" 
                                            name='affairInfo2' 
                                            value='일부' 
                                            id="affairInfo2_2"
                                            onChange={contractInputHandler} 
                                            checked={affairInfo2 === "일부"}
                                        />
                                        <label htmlFor="affairInfo2_2" className="ml-5 mr-10">일부</label>
                                        <input 
                                        type="text" 
                                        disabled={affairInfo2 === "전체"}
                                        name='affairInfo3' 
                                        value={affairInfo3} 
                                        onChange={contractInputHandler} 
                                        data-required="위임보수 적용사무 일부"
                                        className="common-input contract_required"/>
                                    </span>
                                </td>
                            </tr>
                            <tr>
                                <th>분납일정</th>
                                <td colSpan="9">
                                    <textarea 
                                    name='installmentSchedule' 
                                    value={installmentSchedule} 
                                    onChange={contractInputHandler} 
                                    className='common-input w-100p'></textarea>
                                </td>
                            </tr>
                            <tr>
                                <th><i className="required"></i>성공보수 유/무</th>
                                <td>
                                    <select 
                                    data-required="성공보수 유/무"
                                    name='successPay1' 
                                    value={successPay1} 
                                    onChange={contractInputHandler} 
                                    className="common-input w-100p contract_required">
                                        <option value=""></option>
                                        <option value="유">유</option>
                                        <option value="무">무</option>
                                    </select>
                                </td>
                                <th><i className="required"></i>성공보수 지급요건</th>
                                <td colSpan='2'>
                                    <textarea 
                                    data-required="성공보수 지급요건"
                                    name='successPay2' 
                                    value={successPay2} 
                                    onChange={contractInputHandler} 
                                    className='common-input w-100p contract_required'></textarea>
                                </td>
                                <th><i className="required"></i>부가세</th>
                                <td>
                                    <select 
                                    name='successVAT' 
                                    data-required="부가세"
                                    value={successVAT} 
                                    onChange={contractInputHandler} 
                                    className="common-input w-100p contract_required">
                                        <option value=""></option>
                                        <option value="부가세포함">부가세포함</option>
                                        <option value="부가세별도">부가세별도</option>
                                    </select>
                                </td>
                                <th><i className="required"></i>성공보수 예상금액</th>
                                <td colSpan="2" className="ta-l">
                                    <input type="text" 
                                    name='successPay3' 
                                    value={successPay3} 
                                    onChange={contractInputHandler} 
                                    data-required="성공보수 예상금액"
                                    className="common-input mr-5 contract_required"/>원
                                </td>
                            </tr>
                            <tr>
                                <th><i className="required"></i>재판비용</th>
                                <td colSpan="9" className='ta-l'>
                                    <section className="clearfix">
                                        <CheckLists>
                                            <input type="checkbox" 
                                            checked={trialCost.indexOf('인지액') === -1 ? false : true}
                                            name="trialCost" value="인지액" id="trialCost_1" onChange={()=>{checkToggle('인지액')}}/>
                                            <label htmlFor="trialCost_1">인지액</label>
                                        </CheckLists>
                                        <CheckLists>
                                            <input type="checkbox" 
                                            checked={trialCost.indexOf('송달료') === -1 ? false : true}
                                            name="trialCost" value="송달료" id="trialCost_2" onChange={()=>{checkToggle("송달료")}}/>
                                            <label htmlFor="trialCost_2">송달료</label>
                                        </CheckLists>
                                        <CheckLists>
                                            <input type="checkbox" 
                                            checked={trialCost.indexOf('법원보관금') === -1 ? false : true}
                                            name="trialCost" value="법원보관금" id="trialCost_3" onChange={()=>{checkToggle("법원보관금")}}/>
                                            <label htmlFor="trialCost_3">법원보관금</label>
                                        </CheckLists>
                                        <CheckLists>
                                            <input type="checkbox" 
                                            checked={trialCost.indexOf('경유증표') === -1 ? false : true}
                                            name="trialCost" value="경유증표" id="trialCost_4" onChange={()=>{checkToggle("경유증표")}}/>
                                            <label htmlFor="trialCost_4">경유증표</label>
                                        </CheckLists>
                                        <CheckLists>
                                            <input type="checkbox" 
                                            checked={trialCost.indexOf('등록면허세') === -1 ? false : true}
                                            name="trialCost" value="등록면허세" id="trialCost_5" onChange={()=>{checkToggle("등록면허세")}}/>
                                            <label htmlFor="trialCost_5">등록면허세</label>
                                        </CheckLists>
                                        <CheckLists>
                                            <input type="checkbox" 
                                            checked={trialCost.indexOf('등기신청수수료') === -1 ? false : true}
                                            name="trialCost" value="등기신청수수료" id="trialCost_6" onChange={()=>{checkToggle("등기신청수수료")}}/>
                                            <label htmlFor="trialCost_6">등기신청수수료</label>
                                        </CheckLists>
                                        <CheckLists>
                                            <input type="checkbox" 
                                            checked={trialCost.indexOf('민사예납금') === -1 ? false : true}
                                            name="trialCost" value="민사예납금" id="trialCost_7" onChange={()=>{checkToggle("민사예납금")}}/>
                                            <label htmlFor="trialCost_7">민사예납금</label>
                                        </CheckLists>
                                        <CheckLists>
                                            <input type="checkbox" 
                                            checked={trialCost.indexOf('집행비') === -1 ? false : true}
                                            name="trialCost" value="집행비" id="trialCost_8" onChange={()=>{checkToggle("집행비")}}/>
                                            <label htmlFor="trialCost_8">집행비</label>
                                        </CheckLists>
                                        <CheckLists>
                                            <input type="checkbox" 
                                            checked={trialCost.indexOf('공탁금') === -1 ? false : true}
                                            name="trialCost" value="공탁금" id="trialCost_9" onChange={()=>{checkToggle("공탁금")}}/>
                                            <label htmlFor="trialCost_9">공탁금</label>
                                        </CheckLists>
                                        <CheckLists>
                                            <input type="checkbox" 
                                            checked={trialCost.indexOf('신문공고비') === -1 ? false : true}
                                            name="trialCost" value="신문공고비" id="trialCost_10" onChange={()=>{checkToggle("신문공고비")}}/>
                                            <label htmlFor="trialCost_10">신문공고비</label>
                                        </CheckLists>
                                        <CheckLists>
                                            <input type="checkbox" 
                                            checked={trialCost.indexOf('경매예납금') === -1 ? false : true}
                                            name="trialCost" value="경매예납금" id="trialCost_11" onChange={()=>{checkToggle("경매예납금")}}/>
                                            <label htmlFor="trialCost_11">경매예납금</label>
                                        </CheckLists>
                                        <CheckLists>
                                            <input type="checkbox" 
                                            checked={trialCost.indexOf('녹취비용') === -1 ? false : true}
                                            name="trialCost" value="녹취비용" id="trialCost_12" onChange={()=>{checkToggle("녹취비용")}}/>
                                            <label htmlFor="trialCost_12">녹취비용</label>
                                        </CheckLists>
                                        <CheckLists>
                                            <input type="checkbox" 
                                            checked={trialCost.indexOf('기타') === -1 ? false : true}
                                            name="trialCost" value="기타" id="trialCost_13" onChange={()=>{checkToggle("기타")}}/>
                                            <label htmlFor="trialCost_13">기타</label>
                                        </CheckLists>
                                    </section>
                                </td>
                            </tr>
                            <tr>
                                <th>기타보수</th>
                                <td colSpan="9">
                                    <table className='table'>
                                        {
                                            otherPay.map((el, idx) => (
                                                <tr key={idx}>
                                                    <th>
                                                        {
                                                            idx === 0
                                                            ?   <button style={buttonStyle} 
                                                                onClick={()=>{otherPayRowH('plus', idx)}}
                                                                className="default-btn-green mr-5">+</button>
                                                            :   <button style={buttonStyle} 
                                                                onClick={()=>{otherPayRowH('minus', idx)}}
                                                                className="default-btn-red mr-5">-</button>
                                                            }
                                                        (필요시) 진행업무
                                                    </th>
                                                    <td><input 
                                                    type="text" 
                                                    value={el.articles}
                                                    onChange={(e)=>{changeOtherPay(e, idx, "articles")}}
                                                    className="common-input mr-5"/>항</td>
                                                    <th>(필요시) 약정금</th>
                                                    <td><input 
                                                    type="text" 
                                                    value={el.articlesMoney}
                                                    onChange={(e)=>{changeOtherPay(e, idx, "articlesMoney")}}
                                                    className="common-input mr-5"/>원</td>
                                                </tr>
                                            ))
                                        }
                                    </table>
                                </td>
                            </tr>
                            <tr>
                                <th>특약사항</th>
                                <td colSpan="9">
                                    <input type="text" 
                                    name="etcCon"
                                    value={etcCon}
                                    onChange={contractInputHandler} 
                                    className="common-input w-100p"/>
                                </td>
                            </tr>
                            <tr>
                                <th>최종약정서</th>
                                <td colSpan="10" className="ta-l">
                                    <button className="default-btn-red mr-5">파일삭제</button>
                                    <button className="default-btn-green" onClick={()=>{
                                        document.querySelector('#agreement-upload').click();
                                    }}>파일업로드</button>
                                    <input type="file" className="ir_so" id="agreement-upload"/>
                                </td>
                            </tr>
                        </table>
                        {/* // contract-info-table */}

                        {/* 법리검토보고서 테이블 */}
                        <LegalReportTable />
                        {/* // 법리검토보고서 테이블 */}
                    </section>
            }
            {modal.status && <Modal refreshFunction={closeModal} modalText={modal.text}/>}
        </div>
    )
}

export default ContractManagePart
