import React, { useMemo } from "react";
import SearchBox from "../../utils/SearchBox";
import './Sections/ContractChangeListPage.css';

function ContractChangeListPage() {
    const renderTable1 = () => {
        const results = [];
        for (let i = 0; i < 20; i++) {
            results.push(
                <tr key={i}>
                    <td>11111111</td>
                    <td>홍길동</td>
                    <td>1111111111</td>
                </tr>
            );
        }
        return results;
    }

    const renderTable2 = () => {
        const results = [];
        for (let i = 0; i < 20; i++) {
            results.push(
                <tr key={i}>
                    <td>2021-01-01</td>
                    <td>1,000,000</td>
                    <td>1,000,000</td>
                    <td>1,000,000</td>
                </tr>
            );
        }
        return results;
    }

    const renderTable3 = () => {
        const results = [];
        for (let i = 0; i < 20; i++) {
            results.push(
                <tr key={i}>
                    <td>2021-01-01</td>
                    <td>1,000,000</td>
                    <td>1,000,000</td>
                    <td>1,000,000</td>
                </tr>
            );
        }
        return results;
    }

    const tableStyle1 = useMemo(() => ({float: 'left', width: 'calc(25% - 10px)'}), []);
    const tableStyle2 = useMemo(() => ({float: 'left', width: '37.5%'}), []);

    return (
        <div className="page-container contract-change-list-page">
            <SearchBox option={{
                dateStart: true,
                dateEnd: true,
                searchText: true
            }}/>

            <div className="table-title clearfix">
                <p className="title-txt">약정변경</p>
            </div>  
        
            <div className="table_wrapper clearfix">
                <table className="contract-change-table1 table" style={tableStyle1}>
                    <thead>
                        <tr>
                            <th rowSpan="2">의뢰인번호</th>
                            <th rowSpan="2">의뢰인</th>
                            <th rowSpan="2">약정서관리번호</th>
                        </tr>
                    </thead>
                    <tbody>
                        {renderTable1()}
                    </tbody>
                </table>

                <table className="contract-change-table2 table mr-5 ml-5"  style={tableStyle2}>
                    <thead>
                        <tr><th colSpan="4">변경 전</th></tr>
                        <tr>
                            <th>약정일자</th>
                            <th>약정금액</th>
                            <th>잔금</th>
                            <th>환불</th>
                        </tr>
                    </thead>
                    <tbody>
                        {renderTable2()}
                    </tbody>
                </table>

                <table className="contract-change-table3 table"  style={tableStyle2}>
                    <thead>
                        <tr><th colSpan="4">변경 후</th></tr>
                        <tr>
                            <th>변경일자</th>
                            <th>약정변경금액</th>
                            <th>잔금</th>
                            <th>환불(예정)</th>
                        </tr>
                    </thead>
                    <tbody>
                        {renderTable3()}
                    </tbody>
                </table>
            </div>
        </div>
        
    )
}

export default ContractChangeListPage
