import React, { useState } from "react";
import DatepickerCal from './DatepickerCal';
import './datepicker.css';
import { channel, consultResult, employee, incomeKinds, payWay, paySort, caseName1, caseName2 } from '../../Datas/datas';
import styled from 'styled-components';

const SearchBoxWrap = styled.div`
  padding: 15px 10px;
`;
const Wave = styled.span`
  margin: 0 5px;
  font-size: 18px;
`;

function SearchBox(props) {
  const searchOptions = props.option;

  const [ChannelOption, setChannelOption] = useState(channel);
  const [ConsultResult, setConsultResult] = useState(consultResult);
  const [EmployeeList, setEmployeeList] = useState(employee);
  const [IncomeKinds, setIncomeKinds] = useState(incomeKinds);
  const [CaseName1, setCaseName1] = useState(caseName1);
  const [CaseName2, setCaseName2] = useState(caseName2);

  const [dateToStart, setDateToStart] = useState(''); 
  const [dateToEnd, setDateToEnd] = useState(''); 
  
  const startDateHandler = (date) => { setDateToStart(date) }; // 검색일정 시작값 설정 함수
  const endDateHandler = (date) => { setDateToEnd(date) }; // 검색일정 끝나는값 설정 함수

  const submitSearchs = () => {
    let startDateNumbering = +dateToStart.replace(/-/gi,"");
    let endDateNumbering = +dateToEnd.replace(/-/gi,"");

    if (startDateNumbering > endDateNumbering) {
      alert('검색 시작일은 검색종료일보다 이후일 수 없습니다. 다시 확인해주세요.');
      return false;
    }

    let dataToSubmit = {
      startDate: dateToStart,
      endDate: dateToEnd
    }

    console.log('검색조건 출력', dataToSubmit);
  };

  return (
    <SearchBoxWrap className="box_el">
      <div className="flex-con">
        { searchOptions.dateStart && <DatepickerCal dateRefresh={startDateHandler} className="date-start"/> }

        { searchOptions.dateEnd && 
          <>
          <Wave>~</Wave>
          <DatepickerCal className="date-end" dateRefresh={endDateHandler}/> 
          </>
        }

        {searchOptions.incomeKinds &&
          <select className='common-input ml-10'>
            <option value="">수임료구분</option>
            { IncomeKinds.map((el,idx) => ( <option value={el} key={idx}>{el}</option> )) }
          </select>
        }

        {searchOptions.route &&
          <select className='common-input ml-10'>
            <option value="">유입경로</option>
            { ChannelOption.map((el,idx) => ( <option value={el} key={idx}>{el}</option> )) }
          </select>
        }

        {searchOptions.isNew &&
          <select className='common-input ml-10'>
            <option value="">신규여부</option>
            <option value="신규">신규</option>
            <option value="기존">기존</option>
          </select>
        }

        {searchOptions.consultResult1 &&
          <select className='common-input ml-10'>
            <option value="">사건명(대)</option>
            { CaseName1.map((el,idx) => ( <option value={el} key={idx}>{el}</option> )) }
          </select>
        }

        {searchOptions.caseName1 &&
          <select className='common-input ml-10'>
            <option value="">사건명(중)</option>
            { CaseName2.map((el,idx) => ( <option value={el} key={idx}>{el}</option> )) }
          </select>
        }

        {searchOptions.caseName2 &&
          <select className='common-input ml-10'>
            <option value="">사건명(중)</option>
            { CaseName2.map((el,idx) => ( <option value={el} key={idx}>{el}</option> )) }
          </select>
        }

        {searchOptions.jeopsuEmployee &&
          <select className='common-input ml-10'>
            <option value="">접수담당자</option>
            { EmployeeList.map((el,idx) => ( <option value={el} key={idx}>{el}</option> )) }
          </select>
        }

        {searchOptions.searchText &&
          <input type='text' className='common-input ml-10 w-300 mr-10'/>
        }
        <button className='default-btn' onClick={submitSearchs}>검색</button>
      </div>
    </SearchBoxWrap>
  );
}

export default SearchBox;
