import React, { useState, useEffect } from 'react'
import DatePicker from "react-datepicker";
import { ko } from "date-fns/esm/locale";

function DatepickerCal(props) {
    const [Dates, setDates] = useState(new Date());
    const dateToString = (date) => {
        if (!date) {
            setDates(new Date());
            return (new Date()).getFullYear() + '-' + ((new Date()).getMonth() + 1).toString().padStart(2, '0') + '-' + (new Date()).getDate().toString().padStart(2, '0')
        }
        return date.getFullYear() + '-' + (date.getMonth() + 1).toString().padStart(2, '0') + '-' + date.getDate().toString().padStart(2, '0')
    };

    useEffect(() => {
        props.dateRefresh(dateToString(Dates));
    }, [Dates]);

    return (
        <>
            <DatePicker 
                disabled={props.disabled}
                locale={ko}             // 언어설정 기본값은 영어
                dateFormat="yyyy-MM-dd"	// 날짜 형식 설정
                className="input-datepicker common-input w-100p" // 클래스 명 지정 css주기 위해
                selected={Dates}	// value
                onChange={
                    (date) => {
                        setDates(date);
                    }
                }
            />
        </>
    )
}

export default DatepickerCal
