import React, { useState } from 'react';
import { parseOnlyNumber } from '../../utils/unitParse';
import CaseBox from './Sections/CaseBox';

function CaseSettingPage() {

    const [caseLists, setCaseLists] = useState([
        {caseCode: 123, caseBig: '민사', caseMiddle:'공사대금', caseSortNumber: 1010},
        {caseCode: 5678, caseBig: '민사신청', caseMiddle:'기타', caseSortNumber: 1160}
    ]);
    const [caseBig, setCaseBig] = useState('');
    const [caseMiddle, setCaseMiddle] = useState('');
    const [caseSortNumber, setCaseSortNumber] = useState();

    const submitCaseHandler = () => {
        if (!caseBig || !caseMiddle || !caseSortNumber) {
            return alert('모든 내용을 입력해주세요!');
        }
        let dataToSubmit = {
            caseBig,
            caseMiddle,
            caseSortNumber
        };
        console.log('저장할 사건케이스 정보 출력', dataToSubmit);
    };

    return (
        <div className='page-container'>
            {/* 사건케이스 추가 start */}
            <div className="common-part mb-30">
                <section className="part-title clearfix">
                    <p className="title-txt">사건케이스 추가</p>
                </section>
                <section className="part-content">
                    <table className="table card-plus-table">
                        <tr>
                            <th>대분류</th>
                            <th>중분류</th>
                            <th>정렬순서(1~99999)</th>
                        </tr>
                        <tr>
                            <td>
                                <input type="text" 
                                className="common-input w-170" 
                                value={caseBig} 
                                onChange={(e)=>{setCaseBig(e.target.value)}}/>
                            </td>
                            <td>
                                <input type="text" 
                                className="common-input w-170"
                                value={caseMiddle} 
                                onChange={(e)=>{setCaseMiddle(e.target.value)}}/>
                            </td>
                            <td>
                                <input type="text" 
                                className="common-input w-170"
                                value={caseSortNumber} 
                                maxLength='5'
                                onChange={(e)=>{setCaseSortNumber(parseOnlyNumber(e.target.value))}}/>
                            </td>
                        </tr>
                        <tr>
                            <td colSpan="3">
                                <button
                                    onClick={submitCaseHandler}
                                    className="default-btn-blue big-btn mt-5 mb-5">저장
                                </button>
                            </td>
                        </tr>
                    </table>
                </section>
            </div>
            {/* 사건케이스 추가 end */}

            {/* 사건케이스 관리 start*/}
            <div className="common-part">
                <section className="part-title clearfix">
                    <p className="title-txt">사건케이스 관리</p>
                </section>
                <section className="part-content">
                    <table className="table card-manage-table">
                        <tr>
                            <th>대분류</th>
                            <th>중분류</th>
                            <th>정렬</th>
                            <th>관리</th>
                        </tr>

                        {caseLists.length > 0 && caseLists.map((el) => (
                            <CaseBox key={el.caseCode} caseInfo={el}/>
                        ))}
                    </table>
                </section>
            </div>
            {/* 사건케이스 관리 end*/}
        </div>
    )
}

export default CaseSettingPage
