import React, { useState } from 'react';
import SearchBox from '../../utils/SearchBox';
import Paging from '../../utils/Paging';

function CallCheckPage() {
    const [CheckBox, setCheckBox] = useState([]);

    const checkboxToggle = (value) => {
        const currentIdx = CheckBox.indexOf(value);
        const copy = [...CheckBox];
        if ( currentIdx === -1 ) {
          copy.push(value);
        } else {
          copy.splice(currentIdx, 1);
        }
        setCheckBox(copy);
      }
      const checkAll = (e) => {
        const isChecked = e.target.checked;
        if (isChecked) {
          const AllCheckLists = Array.from({length: 7}, (v, i) => i); 
          setCheckBox(AllCheckLists);
        } else {
          setCheckBox([]);
        }
      }

    return (
        <div className="page-container">
             <SearchBox option={{
                dateStart: true,
                dateEnd: true,
                searchText: true
             }}/>
              
            <div className="table-title clearfix">
                <p className="title-txt">전화클릭 현황</p>

                <a href="javascript:;" className="default-btn">엑셀다운</a>
                <a href="javascript:;" className="default-btn-red">선택삭제</a>
            </div>

            <div className="table_wrapper">
                <table className="table">
                    <thead>
                        <tr>
                            <th><input type="checkbox" onChange={checkAll}/></th>
                            <th>클릭날짜</th>
                            <th>키워드</th>
                            <th>소스</th>
                            <th>매체</th>
                            <th>위치</th>
                            <th>페이지</th>
                            <th>IP</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>
                                <input type="checkbox" onChange={()=>checkboxToggle(1)} checked={CheckBox.indexOf(1) === -1 ? '' : 'checked'}/>
                            </td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr>
                            <td>
                                <input type="checkbox" onChange={()=>checkboxToggle(2)} checked={CheckBox.indexOf(2) === -1 ? '' : 'checked'}/>
                            </td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr>
                            <td>
                                <input type="checkbox" onChange={()=>checkboxToggle(3)} checked={CheckBox.indexOf(3) === -1 ? '' : 'checked'}/>
                            </td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr>
                            <td>
                                <input type="checkbox" onChange={()=>checkboxToggle(4)} checked={CheckBox.indexOf(4) === -1 ? '' : 'checked'}/>
                            </td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr>
                            <td>
                                <input type="checkbox" onChange={()=>checkboxToggle(5)} checked={CheckBox.indexOf(5) === -1 ? '' : 'checked'}/>
                            </td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr>
                            <td>
                                <input type="checkbox" onChange={()=>checkboxToggle(6)} checked={CheckBox.indexOf(6) === -1 ? '' : 'checked'}/>
                            </td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                        </tr>
                    </tbody>
                </table>
            </div>

        <Paging/>
        </div>

    )
}

export default CallCheckPage
