import React from "react";
import styled from "styled-components";

const FooterWrapper = styled.div`
    background: #111;
    color: #999; 
    text-align: right;
    font-size: 12px; 
    padding : 15px 0;
`;

function Footer() {
  return (
    <FooterWrapper>
      <div className="page-container">
        Copyright © EHYUN ver 1.0 All rights. Reserved.
      </div>
    </FooterWrapper>
  );
}

export default Footer;
