import React from 'react'
import { useLocation } from "react-router-dom";
import './Paging.css';
import { Link } from 'react-router-dom';

function Paging() {
    const { pathname } = useLocation();
    return (
        <div className="paging-wrap">
            <Link to={`${pathname}`} className="paging-el">처음</Link>
            <Link to={`${pathname}`} className="paging-el">이전</Link>
            
            <ul className="paging-count">
                <li><Link to={`${pathname}`} className="paging-el active">1</Link></li>
                <li><Link to={`${pathname}`} className="paging-el">2</Link></li>
                <li><Link to={`${pathname}`} className="paging-el">3</Link></li>
                <li><Link to={`${pathname}`} className="paging-el">4</Link></li>
                <li><Link to={`${pathname}`} className="paging-el">5</Link></li>
                <li><Link to={`${pathname}`} className="paging-el">6</Link></li>
                <li><Link to={`${pathname}`} className="paging-el">7</Link></li>
                <li><Link to={`${pathname}`} className="paging-el">8</Link></li>
                <li><Link to={`${pathname}`} className="paging-el">9</Link></li>
                <li><Link to={`${pathname}`} className="paging-el">10</Link></li>
            </ul>

            <Link to={`${pathname}`} className="paging-el">다음</Link>
            <Link to={`${pathname}`} className="paging-el">맨끝</Link>
        </div>
    )
}

export default Paging
