import React, { useState } from "react";
import SearchBox from "../../utils/SearchBox";
import articleLists from '../../../Datas/articleLists5';
import { centerLists } from '../../../Datas/datas';
import { currentYear, currentMonth } from '../../utils/time';

function BaedangListPage() {
    const [Article, setArticle] = useState(articleLists);
    const [CheckBox, setCheckBox] = useState([]);

    const checkboxToggle = (value) => {
        const currentIdx = CheckBox.indexOf(value);
        const copy = [...CheckBox];
        if ( currentIdx === -1 ) {
          copy.push(value);
        } else {
          copy.splice(currentIdx, 1);
        }
        setCheckBox(copy);
      }

    const renderTable = () => {
        const results = [];
        for (let i = 0; i < 20; i++) {
            results.push(
                <tr key={i}>
                    <td>11111111</td>
                    <td>홍길동</td>
                    <td>1111111111</td>
                </tr>
            );
        }
        return results;
    }

    const checkAll = (e) => {
        const isChecked = e.target.checked;
        if (isChecked) {
            const AllCheckLists = Array.from({length: articleLists.length}, (v, i) => i); 
            setCheckBox(AllCheckLists);
        } else {
            setCheckBox([]);
        }
    } 

    return (
        <div className="page-container baedang-list-page">
            {/* center-baedang */}
            <div className="center-baedang">
                <div className="table-title clearfix mt-0">
                    <p className="title-txt">{currentYear()}년 {currentMonth()}월 배당 현황</p>
                </div>  

                <table className="table">
                    <thead>
                        <tr>
                            <th colSpan="2" className="border-bottom-none"></th>
                            <th>상담/마케팅</th>
                            <th>송무지원실</th>
                            <th>송무1센터</th>
                            <th>송무2센터</th>
                            <th>송무3센터</th>
                            <th>송무4센터</th>
                            <th>특별송무센터</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <th rowSpan="2" className="border-bottom-none">월 목표</th>
                            <th className="border-bottom-none">건수</th>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr>
                            <th className="border-bottom-none">금액</th>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr>
                            <th rowSpan="2" className="border-bottom-none">배당현황</th>
                            <th className="border-bottom-none">건수</th>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr>
                            <th className="border-bottom-none">금액</th>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr>
                            <th rowSpan="2">목표대비</th>
                            <th className="border-bottom-none">건수</th>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr>
                            <th>금액</th>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                        </tr>
                    </tbody>

                </table>
            </div>
            {/* // center-baedang */}

            {/* day-baedang */}
            <div className="day-baedang">
                <div className="table-title clearfix">
                    <p className="title-txt">일일배당현황</p>

                    <a href="javascript:;" className="default-btn">엑셀다운</a>
                </div>  
            
                <SearchBox option={{
                    dateStart: true,
                    dateEnd: true,
                    searchText: true
                }}/>
                <div className="table_wrapper mt-30">
                    <table className="table">
                        <thead>
                            <tr>
                                <th><input type="checkbox" onChange={checkAll}/></th>
                                <th>수임일자</th>
                                <th>이름</th>
                                <th>사건명(대)</th>
                                <th>사건명(중)</th>
                                <th>법적지위</th>
                                <th>약정금액</th>
                                <th>약정서</th>
                                <th>법리검토보고서</th>
                                <th className="w-75">급행정도</th>
                                <th className="w-85">변심가능성</th>
                                <th className="w-75">난이도</th>
                                <th className="w-75">업무량</th>
                                <th>배당센터</th>
                                <th>배당결과</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                Article.map((el, idx) => (
                                    <tr key={idx}>
                                        <td><input type="checkbox" onChange={()=>checkboxToggle(el._id)} checked={CheckBox.indexOf(el._id) === -1 ? '' : 'checked'}/></td>
                                        <td>{el.contractDate}</td>
                                        <td>{el.name}</td>
                                        <td>{el.caseName1}</td>
                                        <td>{el.caseName2}</td>
                                        <td>{el.caseRole}</td>
                                        <td>{el.contractMoney}</td>
                                        <td><a href="javascript:;" className="default-btn-theme">내용</a></td>
                                        <td>
                                            <a href="javascript:;" className="default-btn mr-5">다운</a>
                                            <a href="javascript:;" className="default-btn-theme">내용</a>
                                        </td>
                                        <td>{el.howUrgent}</td>
                                        <td>{el.howChangeable}</td>
                                        <td>{el.difficulty}</td>
                                        <td>{el.workload}</td>
                                        <td>
                                            <select className="common-input w-100p">
                                                <option value=""></option>
                                                {
                                                centerLists.map((option, optionIdx) => (
                                                    <option value={option} selected={el.baedangCenter == option && 'true'}>{option}</option>
                                                ))
                                                }
                                            </select>
                                        </td>
                                        <td>
                                            <select className="common-input w-100p">
                                                <option value=""></option>
                                                <option value="미배당">미배당</option>
                                                <option value="배당완료">배당완료</option>
                                            </select>
                                        </td>
                                    </tr>
                                ))
                            }
                        </tbody>
                    </table>
                </div>
            </div>
            {/* // day-baedang */}


        </div>
    )
}

export default BaedangListPage
