import React, { useState } from "react";
import SearchBox from "../../utils/SearchBox";
import articleLists from '../../../Datas/articleLists2';
import HoverText from '../../utils/HoverText';
import Paging from '../../utils/Paging';
import { lawyer, employee } from '../../../Datas/datas';

function ConsultationListPage() {
  const [Article, setArticle] = useState(articleLists)
  const [CheckBox, setCheckBox] = useState([]);
  const [HoverTextContent, setHoverTextContent] = useState('');
  const [MousePosition, setMousePosition] = useState({x: '', y: ''});

  const checkboxToggle = (value) => {
    const currentIdx = CheckBox.indexOf(value);
    const copy = [...CheckBox];
    if ( currentIdx === -1 ) {
      copy.push(value);
    } else {
      copy.splice(currentIdx, 1);
    }
    setCheckBox(copy);
  }
  const checkAll = (e) => {
    const isChecked = e.target.checked;
    if (isChecked) {
      const AllCheckLists = Array.from({length: articleLists.length}, (v, i) => i); 
      setCheckBox(AllCheckLists);
    } else {
      setCheckBox([]);
    }
  }

  const mouseMoveHandler = (value, e) => {
    setHoverTextContent(value);
    setMousePosition({ x: e.clientX, y: e.clientY });
  }

  return (
    <div className="page-container">
      <SearchBox option={{
        dateStart: true,
        dateEnd: true,
        route: true,
        isNew: true,
        consultResult1: true,
        consultResult2: true,
        jeopsuEmployee: true,
        searchText: true
      }}/>

      <div className="table-title clearfix">
        <p className="title-txt">상담관리</p>

        <a href="javascript:;" className="default-btn">엑셀다운</a>
        <a href="javascript:;" className="default-btn-blue">신규등록</a>
        <a href="javascript:;" className="default-btn-red">선택삭제</a>
        <a href="javascript:;" className="default-btn-theme">전체보기</a>
      </div>

      <div className="table_wrapper">
        <table className="table">
          <thead>
            <tr>
              <th className="w-30"><input type="checkbox" onChange={checkAll}/></th>
              <th className="w-90">접수일자</th>
              <th className="w-70">신규여부</th>
              <th className="w-100">이름</th>
              <th className="w-120">연락처</th>
              <th className="w-130">사건명(중)</th>
              <th className="w-50">지역</th>
              <th className="w-50">성별</th>
              <th className="w-180">접수내용</th>
              <th className="w-180">특이사항</th>
              <th className="w-50">CP 여부</th>
              <th className="w-110">통화상담결과</th>
              <th className="w-90">내사상담결과</th>
              <th className="w-110">접수담당자</th>
              <th className="w-110">상담매니저</th>
              <th>상담변호사</th>
            </tr>
          </thead>
          <tbody>
            {
                Article.map((el,idx)=>(
                    <tr key={idx}>
                        <td><input type="checkbox" onChange={()=>checkboxToggle(el._id)} checked={CheckBox.indexOf(el._id) === -1 ? '' : 'checked'}/></td>
                        <td>{el.date}<br/>[{el.time}]</td>
                        <td>{el.isNew}</td>
                        <td>{el.name}</td>
                        <td>{el.phone}</td>
                        <td>{el.caseName2}</td>
                        <td>{el.area}</td>
                        <td>{el.gender}</td>
                        <td
                          className='ta-l'
                          onMouseEnter={(e)=>{mouseMoveHandler(el.jeopsuCon, e)}} 
                          onMouseMove={(e)=>{mouseMoveHandler(el.jeopsuCon, e)}}
                          onMouseLeave={()=>setHoverTextContent('')} 
                        >
                          <span className='ellipsis-2'>{el.jeopsuCon}</span>
                        </td>
                        <td>{el.etcCon}</td>
                        <td>{el.cp}</td>
                        <td>{el.callConsultationResult}</td>
                        <td>{el.visitConsultationResult}</td>
                        <td>{el.employee}</td>
                        <td>
                          <select className="common-input w-100p">
                            {
                              lawyer.map((lawyerEl, lawyerIdx) => (
                                <option value={lawyerEl}>{lawyerEl}</option>
                              ))
                            }
                          </select>
                        </td>
                        <td>
                          <select className='common-input w-100p'>
                            {
                              employee.map((employeeEl, employeeIdx) => (
                                <option value={employeeEl}>{employeeEl}</option>
                              ))
                            }
                          </select>
                        </td>
                    </tr>
                ))
            }
          </tbody>
        </table>
      </div>
      {HoverTextContent != "" && <HoverText HoverTextContent={HoverTextContent} MousePosition={MousePosition}/>}
      <Paging/>
    </div>
  );
}

export default ConsultationListPage;
