import React, {useState} from 'react'

function CardBox(props) {
    const [cardName, setCardName] = useState(props.cardName);
    const [cardFee, setCardFee] = useState(props.cardFee);

    const updateCardHandler = () => {
        let dataTosubmit = {
            cardCode: props.cardCode,
            cardName,
            cardFee
        };
        console.log('수정할 카드 정보', dataTosubmit);
    };

    const deleteCardHandler = () => {
        if(window.confirm('삭제하시겠습니까?')) {
            console.log('삭제할 카드 ID는?', props.cardCode);
        } 
    };

    return (
        <tr>
            <td>
                <input type="text" 
                    className="common-input w-170" 
                    value={cardName} 
                    onChange={(e)=>{setCardName(e.target.value)}}
                />
            </td>
            <td>
                <input type="text" 
                    className="common-input w-170 mr-5"
                    value={cardFee}
                    onChange={(e)=>{setCardFee(e.target.value)}}
                />%
            </td>
            <td>
                <button
                    onClick={updateCardHandler}
                    className="default-btn-blue mt-5 mb-5 mr-5">수정
                </button>
                <button
                    onClick={deleteCardHandler}
                    className="default-btn-red mt-5 mb-5">삭제
                </button>
            </td>
        </tr>
    )
}

export default CardBox
