import React, { useState, useEffect, useMemo } from 'react'
import { caseName1, channel, consultResult } from '../../../../Datas/datas';
import { parseOnlyPhone } from '../../../utils/unitParse';
import { getDong1, getDong2 } from '../../../utils/getDong';
import ConsultHistory from '../Sections/ConsultHistory.js';
import Modal from '../../../utils/Modal';
import './JeopSuPart.css';

// fontawesome Icon
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimes } from "@fortawesome/free-solid-svg-icons";

let showHideToggle = 0;

const dummyDatas1 = [
    {
      employeeName: "아이보스관리자",
      employeeRole: 1,
      consultDate: "2021-01-01 12:00:00",
      consultCon: "상담내역 테스트1",
    },
    {
      employeeName: "아이보스관리자",
      employeeRole: 2,
      consultDate: "2021-01-01 12:00:00",
      consultCon: "상담내역 테스트1",
    },
    {
      employeeName: "아이보스관리자",
      employeeRole: 3,
      consultDate: "2021-01-01 12:00:00",
      consultCon: "상담내역 테스트1",
    },
    {
      employeeName: "아이보스관리자",
      employeeRole: 4,
      consultDate: "2021-01-01 12:00:00",
      consultCon: "상담내역 테스트1",
    },
  ];
function JeopSuPart(props) {
    const [modal, setModal] = useState({ status: false, text: '' }); // 모달 state
    const [pastConsultModal, setPastConsultModal] = useState(false); // 과거 상담내역 모달 state
    const [uniquenessDirect, setUniquenessDirect] = useState(''); // 특이사항 직접입력
    const [jeopsuContents, setJeopsuContents] = useState(''); // 접수내용 데이터 state
    
    const [standardsInput, setStandardsInput] = useState({  // 접수관리 기본 데이터 state
        jeopsuEmployee: '',
        userRelation: '',
        userArea1: '',
        userArea2: '',
        userName2: '',
        userPhone2: '',
        userGender: '',
        isNewUser: '',
        isCp: false,
    });
    
    const { 
        jeopsuEmployee, 
        userRelation, 
        userArea1,
        userArea2,
        userName2,
        userPhone2,
        userGender,
        isNewUser,
        isCp
    } = standardsInput;
    
    const [detailsInput, setDetailsInput] = useState({  // 접수관리 추가 데이터 state
        uniqueness: '',
        question: '',
        cp1: '',
        cp2: '',
        userJob: '',
        caseBig: '',
        caseAgoDB: '',
        userType: '개인',
        routes: '',
        recommendEmployee: '',
        routesDetail: '',
        keyWords: '',
        sources: '',
        medias: '',
        oneStepResults: ''
    });

    const { 
        uniqueness,
        question,
        cp1,
        cp2,
        userJob,
        caseBig,
        caseAgoDB,
        userType,
        routes,
        recommendEmployee,
        routesDetail,
        keyWords,
        sources,
        medias,
        oneStepResults
    } = detailsInput;

    const closeModal = () => { setModal(false); }; // 모달 닫기 함수

    const inputHandler1 = (e) => { // 접수관리 기본정보 입력관련 함수
        let {name, value} = e.target;
        if (name == 'userPhone2') value = parseOnlyPhone(value);
        else if (name == 'isCp')  value = !isCp;

        setStandardsInput({
            ...standardsInput,
            [name]: value
        });
    };

    const inputHandler2 = (e) => { // 접수관리 추가정보 입력관련 함수
        let {name, value} = e.target;
        setDetailsInput({
            ...detailsInput,
            [name]: value
        });
    };

    const checkRequired = (classNames) => { // 필수값 검사 함수
        let checkEl = document.querySelectorAll(`.${classNames}`);
        for (let i = 0; i < checkEl.length; i++) {
            if (checkEl[i].value === "" && !checkEl[i].disabled) {
                let copy = {...modal};
                copy.status = true;
                copy.text = `${checkEl[i].getAttribute('data-required')}값은 필수입니다.`
                setModal(copy);
                return false;
            }
        }
        return true;
    }
    
    const showHideAllSection = () => { // 창 전체 닫기 / 전체 열기 함수
        let copy = {...props.PartShowAndHide};
        if (showHideToggle === 0) {
            copy = { jeopsu: false, sangdam: false, visitSchedule: false, paymanage: false, contractManage: false, caseManage: false };
            showHideToggle = 1;
        } else {
            copy ={ jeopsu: true, sangdam: true, visitSchedule: true, paymanage: true, contractManage: true, caseManage: true };
            showHideToggle = 0;
        }
        props.setPartShowAndHide(copy);
    };

    const submitDefaultInfo = () => { // 접수관리 기본정보 submit 함수
        if ( checkRequired('required_1') ) {
            let dataToSumbit = Object.assign(standardsInput, { userName: props.userName, userPhone: props.userPhone });
            console.log('접수관련 기본정보 출력', dataToSumbit);
        }
    };

    const jeopsuConSubmitHandler = () => { // 접수내용 submit 함수
        if (jeopsuContents.trim() === "" ) {
            let copy = {...modal};
            copy.status = true;
            copy.text = '내용을 입력해주세요.';
            setModal(copy);
            return false;
        }
        if ( checkRequired('required_1') ) {
            console.log('접수내용 DB저장', jeopsuContents);
        }
    };

    const submitJeopsuAll = () => { // 접수관리 파트 최종저장 submit 함수
        if ( checkRequired('required_2') ) {

            if (detailsInput.routes !== "직원소개") detailsInput.recommendEmployee = "";
            
            if (detailsInput.uniqueness === "직접입력") detailsInput.uniqueness = uniquenessDirect;
            else detailsInput.uniqueness = uniqueness;

            let dataToSumbit = Object.assign(standardsInput, detailsInput, { userName: props.userName, userPhone: props.userPhone });
            console.log('접수관리 입력한 모든 정보들 출력', dataToSumbit);
        }
    }

    useEffect(() => {
        getDong2(document.querySelector('.get-dong-1').value, 'getDongResults1', 'inputNational1');
    }, []);

    const style1 = useMemo(() => ({ padding: '0 20px', fontSize: '15px'}), []);
    const style2 = useMemo(() => ({width: 'calc(50% - 5px)'}), []);
    const style3 = useMemo(() => ({ width: '90%', verticalAlign: '-12px', height: '120px' }), []);
    const style4 = useMemo(() => ({width: 'calc(50% - 5px)', display: 'none'}), []);
    const style5 = useMemo(() => ({ marginTop: '-20px'}), []);

    return (
        <div className="common-part mb-20" style={style5}>
            <section className="part-title clearfix">
                <p className="title-txt">접수관리</p>
                <p className="btn-wrapper">
                    <span className="default-btn-theme">복사</span>
                    <span className="default-btn-theme ml-5" onClick={showHideAllSection}>
                        {showHideToggle === 0 ? '전체닫기' : '전체보기'}
                    </span>
                    <span 
                        className={ `default-btn-theme ml-5 ${props.PartShowAndHide.jeopsu ? 'minus' : 'plus'}` }
                        onClick={()=>{props.partToggle('jeopsu')}}>
                    </span>
                </p>
            </section>

            {
                props.PartShowAndHide.jeopsu
                &&
                    <section className="part-content">
                    {/* standard-jeopsu-info-table */}
                    <table className="table standard-jeopsu-info-table">
                        <colgroup>
                            <col className="w-10p"/>
                            <col className="w-7p"/>
                            <col className="w-13p"/>
                            <col className="w-7p"/>
                            <col className="w-15p"/>
                            <col className="w-10p"/>
                            <col className="w-14p"/>
                            <col className="w-7p"/>
                            <col className="w-6p"/>
                            <col className="w-6p"/>
                            <col className="w-5p"/>
                        </colgroup>
                        <tr>
                            <th colSpan="4">접수담당자</th>
                            <td colSpan="7" className='ta-l'>
                                <select className="common-input mr-10" name="jeopsuEmployee" value={jeopsuEmployee} onChange={inputHandler1}>
                                    <option value=""></option>
                                    <option value="접수담당자1">접수담당자1</option>
                                    <option value="접수담당자2">접수담당자2</option>
                                    <option value="접수담당자3">접수담당자3</option>
                                </select>
                                <button className="default-btn-blue" onClick={submitDefaultInfo}>접수관리 기본정보 저장</button>
                            </td>
                        </tr>
                        <tr>
                            <th rowSpan="2">기본정보</th>
                            <th><i className="required"></i>이름</th>
                            <td><input type="text" className="common-input w-100p required_1 required_2" data-required="이름" value={props.userName} onChange={(e)=>{props.setUserName(e.target.value)}}/></td>
                            <th><i className="required"></i>연락처</th>
                            <td>
                                <input type="text" className="common-input w-100p required_1 required_2" maxlength="15" data-required="연락처"
                                value={props.userPhone} 
                                onChange={(e)=>{props.setUserPhone(parseOnlyPhone(e.target.value))}}    
                                /></td>
                            <th>관계</th>
                            <td>
                                <select className="common-input w-100p" name="userRelation" value={userRelation} onChange={inputHandler1}>
                                    <option value="본인">본인</option>
                                    <option value="대리인">대리인</option>
                                </select>
                            </td>
                            <th>지역</th>
                            <td colSpan="3">
                                <select 
                                    className="common-input mr-5 get-dong-1" 
                                    style={style2} 
                                    name="userArea1" 
                                    value={userArea1}
                                    onChange={
                                        (e)=>{
                                            getDong2(e.target.value, 'getDongResults1', 'inputNational1');
                                            inputHandler1(e);
                                        }
                                    }
                                    >
                                    <option value="">선택</option>
                                    {getDong1()}
                                </select>
                                <select className="common-input ml-5 getDongResults1" style={style2} name="userArea2" value={userArea2} onChange={inputHandler1}>
                                </select>
                                <input type="text" placeholder="국가입력" name="userArea2" value={userArea2} onChange={inputHandler1} className="common-input ml-5 inputNational1" style={style4} />
                            </td>
                        </tr>
                        <tr>
                            <th>이름(예비)</th>
                            <td><input type="text" className="common-input w-100p" name="userName2" value={userName2} onChange={inputHandler1} /></td>
                            <th>연락처(예비)</th>
                            <td><input type="text" className="common-input w-100p" name="userPhone2" value={userPhone2} onChange={inputHandler1} maxlength="15"/></td>
                            <th>성별</th>
                            <td>
                                <select className="common-input w-100p" name="userGender" value={userGender} onChange={inputHandler1}>
                                    <option value="">선택</option>
                                    <option value="남성">남성</option>
                                    <option value="여성">여성</option>
                                </select>
                            </td>
                            <th>신규여부</th>
                            <td>
                                <select className="common-input w-100p" name="isNewUser" value={isNewUser} onChange={inputHandler1}>
                                    <option value="">선택</option>
                                    <option value="신규">신규</option>
                                    <option value="기존">기존</option>
                                </select>
                            </td>
                            <th>CP여부</th>
                            <td><input type="checkbox" name="isCp" onChange={inputHandler1} checked={isCp}/></td>
                        </tr>

                    </table>
                    {/* //standard-jeopsu-info-table */}

                    {/* past-consult-table */}
                    <table className="table past-consult-table mt-20">
                        <tr><th colSpan="5">과거상담내역</th></tr>
                        <tr>
                            <td>2021-09-01</td>
                            <td><span className="default-btn-green" onClick={()=>{setPastConsultModal(true)}}>내용보기</span></td>
                            <td>학폭서면</td>
                            <td>기타</td>
                            <td><span className="default-btn-green">바로가기</span></td>
                        </tr>
                        <tr>
                            <td>2021-09-01</td>
                            <td><span className="default-btn-green" onClick={()=>{setPastConsultModal(true)}}>내용보기</span></td>
                            <td>학폭서면</td>
                            <td>기타</td>
                            <td><span className="default-btn-green">바로가기</span></td>
                        </tr>
                    </table>
                    { pastConsultModal && <PastConsult setPastConsultModal={setPastConsultModal}/> }
                    {/* // past-consult-table */}

                    {/* //detail-jeopsu-info-table */}
                    <table className='table detail-jeopsu-info-table mt-10'>
                        <tr>
                            <th rowSpan="2">접수내용</th>
                            <td colSpan="8" className='p-10'>
                                <ConsultHistory dummyDatas={dummyDatas1}/>
                            </td>
                        </tr>        
                        <tr>
                            <th>입력</th>
                            <td colSpan="7" className="p-10">
                                <textarea 
                                className="common-input" 
                                style={style3} 
                                value={jeopsuContents}
                                onChange={(e)=>{setJeopsuContents(e.target.value)}}
                                ></textarea>
                                <button 
                                    className="default-btn-green big-btn ml-5" 
                                    onClick={jeopsuConSubmitHandler}>
                                    등록
                                </button>
                            </td>
                        </tr>
                        <tr>
                            <th>특이사항</th>
                            <td colSpan="8" className="ta-l">
                                <select className="common-input noted-selection" name="uniqueness" value={uniqueness} onChange={inputHandler2}>
                                    <option value=""></option>
                                    <option value="빠른연락 요청">빠른연락 요청</option>
                                    <option value="신청기한 마감 임박">신청기한 마감 임박</option>
                                    <option value="부재시 문자부탁">부재시 문자부탁</option>
                                    <option value="02 번호로 연락요청">02 번호로 연락요청</option>
                                    <option value="010 번호로 연락요청">010 번호로 연락요청</option>
                                    <option value="카톡으로 연락요청">카톡으로 연락요청</option>
                                    <option value="30분 내 내사 원함">30분 내 내사 원함</option>
                                    <option value="위임의지 강함">위임의지 강함</option>
                                    <option value="직접입력">직접입력</option>
                                </select>
                                <br />
                                <input type="text" 
                                value={uniqueness === "직접입력" ? uniquenessDirect : uniqueness}
                                readOnly={uniqueness !== "직접입력" && 'true'}
                                onChange={(e)=>{setUniquenessDirect(e.target.value)}}
                                className="common-input mt-5 w-100p" />
                            </td>
                        </tr>
                        <tr>
                            <th>문의사항</th>
                            <td colSpan="8">
                                <textarea className="common-input w-100p" name="question" value={question} onChange={inputHandler2}></textarea>
                            </td>
                        </tr>
                        <tr>
                            <th>CP내용</th>
                            <td colSpan="8" className="ta-l">
                                <select className="common-input" name="cp1" value={cp1} onChange={inputHandler2}>
                                    <option value=""></option>
                                    <option value="환불요청">환불요청</option>
                                    <option value="담당자 연락 안됨">담당자 연락 안됨</option>
                                    <option value="담당자 잦은 교체">담당자 잦은 교체</option>
                                    <option value="계약 내용과 상이">계약 내용과 상이</option>
                                    <option value="송달료 등의 비용에 대한 항의">송달료 등의 비용에 대한 항의</option>
                                    <option value="재배당 후 연락 늦음">재배당 후 연락 늦음</option>
                                    <option value="성격 급한 고객">성격 급한 고객</option>
                                    <option value="진행속도가 느림">진행속도가 느림</option>
                                </select>     
                                <br />
                                <textarea className="common-input w-100p mt-5" name="cp2" value={cp2} onChange={inputHandler2}></textarea>
                            </td>
                        </tr>
                        <tr>
                            <th>추가정보</th>
                            <th>직업</th>
                            <td>
                                <select className="common-input w-100p" name="userJob" value={userJob} onChange={inputHandler2}>
                                    <option value=""></option>
                                    <option value="회사원">회사원</option>
                                    <option value="자영업">자영업</option>
                                    <option value="공무원">공무원</option>
                                    <option value="무직">무직</option>
                                    <option value="학생">학생</option>
                                    <option value="전문직">전문직</option>
                                    <option value="군인">군인</option>
                                    <option value="기타">기타</option>
                                </select>
                            </td>
                            <th>사건명(대)</th>
                            <td>
                                <select className="common-input w-100p" name="caseBig" value={caseBig} onChange={inputHandler2}>
                                    {caseName1.map((el,idx)=> (<option value={el}>{el}</option>))}
                                </select>
                            </td>
                            <th>사건명(구디비용)</th>
                            <td><input type="text" className="common-input w-100p" name="caseAgoDB" value={caseAgoDB} onChange={inputHandler2}/></td>
                            <th>고객유형</th>
                            <td>
                                <select className="common-input w-100p" name="userType" value={userType} onChange={inputHandler2}>
                                    <option value="개인">개인</option>
                                    <option value="법인">법인</option>
                                </select>
                            </td>
                        </tr>
                        <tr>
                            <th rowSpan="2">유입경로</th>
                            <th><i className="required"></i>경로</th>
                            <td>
                                <select className="common-input w-100p required_2" data-required="경로" name="routes" value={routes} onChange={inputHandler2}>
                                    {channel.map((el, idx)=> (
                                        <option key={idx} value={el}>{el}</option>
                                    ))}
                                </select>
                            </td>
                            <th>직원소개</th>
                            <td>
                                <input type="text" 
                                className="common-input w-100p"
                                readOnly={routes !== "직원소개" && 'true'}
                                name="recommendEmployee" 
                                value={routes === "직원소개" ? recommendEmployee : ''} 
                                onChange={inputHandler2}
                                />
                            </td>
                            <th>경로(상세)</th>
                            <td colSpan="3"><input type="text" className="common-input w-100p" name="routesDetail" value={routesDetail} onChange={inputHandler2} /></td>
                        </tr>
                        <tr>
                            <th>키워드(홈페이지)</th>
                            <td><input type="text" className="common-input w-100p" name="keyWords" value={keyWords} onChange={inputHandler2} /></td>
                            <th>소스</th>
                            <td colSpan="2"><input type="text" className="common-input w-100p" name="sources" value={sources} onChange={inputHandler2} /></td>
                            <th>매체</th>
                            <td colSpan="2"><input type="text" className="common-input w-100p" name="medias" value={medias} onChange={inputHandler2}/></td>
                        </tr>
                        <tr>
                            <th>1차 상담 결과</th>
                            <td colSpan="8" className="ta-l">
                                <select className="common-input" name="oneStepResults" value={oneStepResults} onChange={inputHandler2}>
                                    {
                                        consultResult.map((el, idx) => (
                                            <option value={el}>{el}</option>
                                        ))
                                    }
                                </select>
                                <span className="default-btn-blue ml-5" style={style1} onClick={submitJeopsuAll}>제출</span>
                            </td>
                        </tr>
                    </table>
                    {/* //detail-jeopsu-info-table */}

                </section>
            }
            
        {modal.status && <Modal refreshFunction={closeModal} modalText={modal.text}/>}
        </div>
    )
}

function PastConsult(props) {
    return (
        <div className="past-consult-modal">
            <p className='btn-wrap'>
                <span className='close-modal' onClick={()=>{props.setPastConsultModal(false)}}><FontAwesomeIcon icon={faTimes}/></span>
            </p>
            <p className="consult-date">접수일 : 2021-08-11</p>
            <p className="consult-info mb-30">
                <span>접수상담 : 이지현</span> /
                <span>상담매니저 : pass 1팀</span> /
                <span>변호사 : </span>
            </p>

            <table className='table past-jeopsu-consult mb-10'>
                <tr>
                    <th>작성일</th>
                    <th>작성자</th>
                    <th>접수상담 내용</th>
                    <th>상담결과</th>
                </tr>
                <tr>
                    <td>2021-01-01 00:00:00</td>
                    <td>이지현</td>
                    <td>결번</td>
                    <td>내사상담</td>
                </tr>
            </table>

            <table className='table past-call-consult mb-10'>
                <tr>
                    <th>작성일</th>
                    <th>작성자</th>
                    <th>통화상담 내용</th>
                    <th>상담결과</th>
                </tr>
                <tr>
                    <td colSpan="4">작성 내용이 없습니다.</td>
                </tr>
            </table>

            <table className='table past-visit-consult mb-10'>
                <tr>
                    <th>작성일</th>
                    <th>작성자</th>
                    <th>내사상담 내용</th>
                    <th>상담결과</th>
                </tr>
                <tr>
                    <td colSpan="4">작성 내용이 없습니다.</td>
                </tr>
            </table>

        </div>
    )
}

export default JeopSuPart