import React, { useState, useEffect } from 'react'
import './Sections/ConfigLayoutPage.css';

function ConfigLayoutPage() {
    
    const [MainColor, setMainColor] = useState(window.localStorage.getItem("theme") || "#9d1c4c")

    const colorChoiceHandler = (e) => {
        let selectedColor = e.target.attributes.getNamedItem('data-color-value').value;
        setMainColor(selectedColor);
    }

    const submitHandler = (e) => {
        e.preventDefault();
        window.localStorage.setItem('theme', MainColor);
        window.location.replace('/pageOption');
    }
    return (
        <div className="page-container">
            <p className="page-title">페이지 레이아웃 설정</p>
            <form onSubmit={submitHandler} className="layout-config-form">
                <div className="layout-config-box">
                    <input type="radio" name="choice-color" value={MainColor} onChange={colorChoiceHandler} data-color-value="#9d1c4c" id="burgundy" checked={MainColor === "#9d1c4c"}/>
                    <label htmlFor='burgundy'>자주색</label>

                    <input type="radio" name="choice-color" value={MainColor} onChange={colorChoiceHandler} data-color-value="#663399" id="purple" checked={MainColor === "#663399"}/>
                    <label htmlFor='purple'>보라색</label>

                    <input type="radio" name="choice-color" value={MainColor} onChange={colorChoiceHandler} data-color-value="#2196f3" id="blue" checked={MainColor === "#2196f3"}/>
                    <label htmlFor='blue'>파란색</label>

                    <input type="radio" name="choice-color" value={MainColor} onChange={colorChoiceHandler} data-color-value="#4caf50" id="green" checked={MainColor === "#4caf50"}/>
                    <label htmlFor='green'>초록색</label>

                    <input type="radio" name="choice-color" value={MainColor} onChange={colorChoiceHandler} data-color-value="#fb8c00" id="yellow" checked={MainColor === "#fb8c00"}/>
                    <label htmlFor='yellow'>노란색</label>

                    <input type="radio" name="choice-color" value={MainColor} onChange={colorChoiceHandler} data-color-value="#78909c" id="grey" checked={MainColor === "#78909c"}/>
                    <label htmlFor='grey'>회색</label>

                    <input type="radio" name="choice-color" value={MainColor} onChange={colorChoiceHandler} data-color-value="#ef5350" id="red" checked={MainColor === "#ef5350"}/>
                    <label htmlFor='red'>주황색</label>
                </div>
                <button className=" default-btn-theme mt-20">변경하기</button>
            </form>
        </div>
    )
}

export default ConfigLayoutPage
