import React, { useState, useMemo } from 'react';
import DatepickerCal from '../../../utils/DatepickerCal';
import { Link } from 'react-router-dom';

function VisitSchedulePart(props) {
    const [visitHistoryDatas, setvisitHistoryDatas] = useState([
        {
            visitDate: '2021-01-01',
            visitTime: '08:00',
            visitSort: '돌발',
            isVisit: '',
            reasonCancel: '일정변경',
            consultRoom: '상담실예시',
            carInfo: '소나타 차량'
        },
        {
            visitDate: '2021-01-01',
            visitTime: '08:00',
            visitSort: '돌발',
            isVisit: '',
            reasonCancel: '일정변경',
            consultRoom: '상담실예시',
            carInfo: '소나타 차량'
        },
        {
            visitDate: '2021-01-01',
            visitTime: '08:00',
            visitSort: '돌발',
            isVisit: '',
            reasonCancel: '일정변경',
            consultRoom: '상담실예시',
            carInfo: '소나타 차량'
        },
    ]);

    const [visitDate, setVisitDate] = useState(''); // 내사일정 (일자)
    const [visitHour, setVisitHour] = useState('08'); // 내사일정 (몇시)
    const [visitMinute, setVisitMinute] = useState('00'); // 내사일정 (몇분)

    const dateHandler = (date) => { setVisitDate(date); }; // datepicker state 갱신 함수

    const plusVisitSchedule = () => { // 내사일정 submit 함수
        if ( !visitDate || !visitHour || !visitMinute ) {
            alert('내사일정을 모두 입력해주세요');
        }
        let dataToSubmit = { visitDate: visitDate, visitHour: visitHour, visitMinute: visitMinute };
        console.log('내사일정 저장값 출력', dataToSubmit);
    };

    const changeScheduleFn = (val, index, targets) => {
        let copy = [...visitHistoryDatas];
        copy[index][targets] = val;
        setvisitHistoryDatas(copy);
    };
    const whiteBg = useMemo(() => ({background: "#fff"}), []);
    
    return (
        <div className="common-part mb-20 mt-20">
            <section className="part-title clearfix">
                <p className="title-txt">내사일정</p>
                <p className="btn-wrapper">
                    <Link target="_blank" to={`/reportVisit/123`} rel="noopener noreferrer" className="default-btn-theme">내사상담보고서 출력</Link>
                    <span 
                        className={ `default-btn-theme ml-5 ${props.PartShowAndHide.visitSchedule ? 'minus' : 'plus'}` }
                        onClick={()=>{props.partToggle('visitSchedule')}}>
                    </span>
                </p>
            </section>
            
            {
                props.PartShowAndHide.visitSchedule
                &&
                <section className="part-content">
                    {/* visit-schedule-input-table */}
                    <table className="table visit-schedule-input-table mb-30">
                        <tr>
                            <th>내사일정 작성</th>
                            <th>예상 내사일자</th>
                            <td> <DatepickerCal dateRefresh={dateHandler}/> </td>
                            <th>예상 내사시간</th>
                            <td>
                                <select 
                                    className="common-input" 
                                    value={visitHour} 
                                    onChange={(e)=>{setVisitHour(e.target.value)}}>
                                    <option value="08">08</option>
                                    <option value="09">09</option>
                                    <option value="10">10</option>
                                    <option value="11">11</option>
                                    <option value="12">12</option>
                                    <option value="13">13</option>
                                    <option value="14">14</option>
                                    <option value="15">15</option>
                                    <option value="16">16</option>
                                    <option value="17">17</option>
                                    <option value="18">18</option>
                                    <option value="19">19</option>
                                    <option value="20">20</option>
                                    <option value="21">21</option>
                                    <option value="22">22</option>
                                </select>시
                                <select 
                                    className="common-input ml-10" 
                                    value={visitMinute}
                                    onChange={(e)=>{setVisitMinute(e.target.value)}}>
                                    <option value="00">00</option>
                                    <option value="10">10</option>
                                    <option value="20">20</option>
                                    <option value="30">30</option>
                                    <option value="40">40</option>
                                    <option value="50">50</option>
                                </select>분
                            </td>
                        </tr>
                        <tr><td colSpan='5'><button className="default-btn-blue big-btn mt-5 mb-5" onClick={plusVisitSchedule}>내사일정 추가</button></td></tr>
                    </table>
                    
                    {/* // visit-schedule-input-table */}

                    {/* visit-schedule-history-table */}
                    <table className="table visit-schedule-history-table">
                        <thead>
                            <tr><th colSpan="8">내사 일정</th></tr>
                            <tr>
                                <th className="border-bottom-none">내사 일자</th>
                                <th className="border-bottom-none">내사 시간</th>
                                <th className="border-bottom-none">구분</th>
                                <th className="border-bottom-none">실내사 여부</th>
                                <th className="border-bottom-none">내사취소 사유</th>
                                <th className="border-bottom-none">상담실</th>
                                <th className="border-bottom-none">차량</th>
                                <th className="border-bottom-none">관리</th>
                            </tr>
                        </thead>
                        <tbody> 
                            {
                                visitHistoryDatas.map((el,idx) => (
                                    <tr style={whiteBg}>
                                        <td>{el.visitDate}</td>
                                        <td>{el.visitTime}</td>
                                        <td>
                                            <select 
                                                className="common-input w-100p" 
                                                value={el.visitSort} 
                                                onChange={(e)=>{changeScheduleFn(e.target.value, idx, 'visitSort')}}>
                                                <option value=""></option>
                                                <option value="돌발">돌발</option>
                                                <option value="계획">계획</option>
                                                <option value="재내사">재내사</option>
                                            </select>
                                        </td>
                                        <td>
                                            <select 
                                                className="common-input w-100p" 
                                                value={el.isVisit}
                                                onChange={(e)=>{changeScheduleFn(e.target.value, idx, 'isVisit')}}>
                                                <option value=""></option>
                                                <option value="1">O</option>
                                                <option value="2">X</option>
                                            </select>
                                        </td>
                                        <td>
                                            <select 
                                                className="common-input w-100p" 
                                                value={el.reasonCancel}
                                                onChange={(e)=>{changeScheduleFn(e.target.value, idx, 'reasonCancel')}}>
                                                <option value=""></option>
                                                <option value="일정변경">일정변경</option>
                                                <option value="코로나">코로나</option>
                                                <option value="연락두절">연락두절</option>
                                                <option value="분쟁해결">분쟁해결</option>
                                                <option value="기타">기타</option>
                                            </select>
                                        </td>
                                        <td>
                                            <input type="text" className="common-input w-100p" value={el.consultRoom} onChange={(e)=>{changeScheduleFn(e.target.value, idx, 'consultRoom')}}/></td>
                                        <td><input type="text" className="common-input w-100p" value={el.carInfo} onChange={(e)=>{changeScheduleFn(e.target.value, idx, 'carInfo')}}/></td>
                                        <td><span className="default-btn-red" onClick={()=>{window.confirm('삭제하시겠습니까?')}}>삭제</span></td>
                                    </tr>
                                ))
                            }
                        </tbody>
                    </table>
                    {/* // visit-schedule-history-table */}
                </section>
            }

        </div>
    )
}

export default VisitSchedulePart
