import React from 'react'
import SearchBox from '../../utils/SearchBox';
import styled from 'styled-components';

const DbTitleLine = styled.td`
    background: #565656;
    color:#fff;
    font-size:15px;
`;

function StatisticsByManagerPage() {
    return (
        <div className="page-container">
            <SearchBox option={{
                dateStart: true,
                dateEnd: true,
                searchText: true
            }}/>

            <div className="table-title clearfix">
                <p className="title-txt">통계[상담매니저]</p>
                <a href="javascript:;" className="default-btn">엑셀다운</a>
            </div>

            <table className="table">
                <thead>
                    <tr>
                        <th>상담매니저</th>
                        <th>디비수</th>
                        <th>내사예약수</th>
                        <th>예약률</th>
                        <th>내사수</th>
                        <th>내사율</th>
                        <th>수임수</th>
                        <th>수임율</th>
                        <th>객단가</th>
                        <th>환불</th>
                        <th>매출</th>
                    </tr>
                </thead>
                <tbody>
                    <tr><DbTitleLine colSpan={11}>인콜 DB</DbTitleLine></tr>
                    <tr>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                    </tr>
                    <tr><DbTitleLine colSpan={11}>접수 DB</DbTitleLine></tr>
                    <tr>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                    </tr>
                    <tr><DbTitleLine colSpan={11}>종합</DbTitleLine></tr>
                    <tr>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                    </tr>
                </tbody>
            </table>

        </div>
    )
}

export default StatisticsByManagerPage
